import React, { useEffect, useState } from 'react';
import { Endpoints } from "../API/Endpoints";
import { FetchApi } from "../API/FetchApi";
import { CommonApi } from "../API/CommonApi";
import ToastMessage from "../Utils/ToastMessage";
import Navbar from "../Layout/Navbar";
import Sidebar from "../Layout/Sidebar";

export default function Home() {
    const [profileData, setProfileData] = useState({
        name: '',
        company_name: '',
        brand_name: '',
        gst: '',
        domain: '',
        email: '',
        mobile: '',
        address: '',
        image: null, // for image URL or File
    });

    const [previewImage, setPreviewImage] = useState(null);
    const [isLoading, setIsLoading] = useState(false);
    const token = localStorage.getItem("token");

    const handleChange = (e) => {
        const { name, value, files } = e.target;

        if (name === 'image' && files && files[0]) {
            setPreviewImage(URL.createObjectURL(files[0]));
            setProfileData((prev) => ({ ...prev, image: files[0] }));
        } else {
            setProfileData((prev) => ({ ...prev, [name]: value }));
        }
    };

    const getData = async () => {
        try {
            const response = await FetchApi(Endpoints.Profile, {
                headers: {
                    Authorization: `Bearer ${token}`,
                },
            });

            if (response?.status === true && response?.data) {
                
                localStorage.setItem("user", JSON.stringify(response.data));
                const {
                    name, company_name, brand_name, gst,
                    domain, email, mobile, address, image
                } = response.data;

                setProfileData((prev) => ({
                    ...prev,
                    name,
                    company_name,
                    brand_name,
                    gst,
                    domain,
                    email,
                    mobile,
                    address,
                    image,
                }));
            } else {
                ToastMessage.Error(response?.message || "Failed to load About Us data.");
            }
        } catch (error) {
            if (error?.response?.status === 401) {
                ToastMessage.Error("Session expired. Please log in again.");
            } else {
                ToastMessage.Error(error?.response?.data?.message || "Error loading data.");
            }
            console.error("Fetch Error:", error);
        }
    };

    useEffect(() => {
        getData();
    }, []);

    const handleSubmit = async (e) => {
        e.preventDefault();
        setIsLoading(true);

        const formData = new FormData();
        formData.append("brand_name", profileData.brand_name);
        formData.append("company_name", profileData.company_name);
        formData.append("domain", profileData.domain);
        formData.append("email", profileData.email);
        formData.append("mobile", profileData.mobile);
        formData.append("address", profileData.address);
        formData.append("gst", profileData.gst);

        if (profileData.image && typeof profileData.image !== 'string') {
            formData.append("image", profileData.image);
        }

        try {
            const response = await CommonApi(Endpoints.ProfileUpdate, formData);
            if (response?.status === true) {
                ToastMessage.Success("About Us updated successfully!");
                getData();
            } else {
                ToastMessage.Error(response?.message || "Failed to update About Us.");
            }
        } catch (error) {
            ToastMessage.Error(error?.response?.data?.message || "Something went wrong.");
            console.error("Submit Error:", error);
        } finally {
            setIsLoading(false);
        }

    };
    return (
        <div class="page-wrapper">
            <Navbar />
            <Sidebar />
            <div class="page-wrapper">
                <div class="main-content">
                    <div class="row">
                        <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                            <div class="card table-card">
                                <div class="card-header pb-0">
                                    <a href="custom-notification-form.html">

                                        <h4>Users</h4>
                                    </a>
                                </div>

                                <div class="card-body">
                                    <form onSubmit={handleSubmit}>
                                        <div className="row">
                                       
                                            <div className="col-md-6">
                                                <div className="mb-3">
                                                    <label className="form-label">Upload Logo</label>
                                                    <input
                                                        type="file"
                                                        name="image"
                                                        onChange={handleChange}
                                                        className="form-control"
                                                        accept="image/png, image/jpeg, image/jpg, image/gif, image/webp"
                                                    />
                                                </div>
                                            </div>
                                            {(previewImage || profileData.image) && (
                                            <div className="col-md-6">
                                                <div className="mb-3">
                                                    <label className="form-label">Current Logo</label>
                                                    <div>
                                                        <img
                                                            src={previewImage || profileData.image}
                                                            alt="Logo Preview"
                                                            style={{ height: "100px", objectFit: "contain", border: '1px solid #ddd', padding: '5px' }}
                                                        />
                                                    </div>
                                                </div>
                                            </div>
                                        )}
                                            <div className="col-md-6">
                                                <div className="mb-3">
                                                    <label className="form-label">Brand Name</label>
                                                    <input
                                                        type="text"
                                                        name="brand_name"
                                                        value={profileData.brand_name}
                                                        onChange={handleChange}
                                                        className="form-control"
                                                        required
                                                        placeholder="Enter Brand Name"
                                                    />
                                                </div>
                                            </div>
                                            <div className="col-md-6">
                                                <div className="mb-3">
                                                    <label className="form-label">Company Name</label>
                                                    <input
                                                        type="text"
                                                        name="company_name"
                                                        value={profileData.company_name}
                                                        onChange={handleChange}
                                                        className="form-control"
                                                        required
                                                        placeholder="Enter Company Name"
                                                    />
                                                </div>
                                            </div>
                                            <div className="col-md-6">
                                                <div className="mb-3">
                                                    <label className="form-label">GST Number</label>
                                                    <input
                                                        type="text"
                                                        name="gst"
                                                        value={profileData.gst}
                                                        onChange={handleChange}
                                                        className="form-control"
                                                        required
                                                        placeholder="Enter GST Number"
                                                    />
                                                </div>
                                            </div>
                                            <div className="col-md-6">
                                            <div className="mb-3">
                                                <label className="form-label">Email Id</label>
                                                <input
                                                    type="email"
                                                    name="email"
                                                    value={profileData.email}
                                                    onChange={handleChange}
                                                    className="form-control"
                                                    required
                                                    placeholder="Enter Email Id"
                                                />
                                            </div>
                                        </div>

                                            <div className="col-md-6">
                                                <div className="mb-3">
                                                    <label className="form-label">Address</label>
                                                    <input
                                                        type="text"
                                                        name="address"
                                                        value={profileData.address}
                                                        onChange={handleChange}
                                                        className="form-control"
                                                        required
                                                        placeholder="Enter Address"
                                                    />
                                                </div>
                                            </div>
                                            <div className="col-md-6">
                                                <div className="mb-3">
                                                    <label className="form-label">Domain</label>
                                                    <input
                                                        type="text"
                                                        name="domain"
                                                        value={profileData.domain}
                                                        onChange={handleChange}
                                                        className="form-control"
                                                        required
                                                        placeholder="Enter Domain"
                                                    />
                                                </div>
                                            </div>
                                            <div className="col-md-6">
                                                <div className="mb-3">
                                                    <label className="form-label">Contact No</label>
                                                    <input
                                                        type="tel"
                                                        name="mobile"
                                                        value={profileData.mobile}
                                                        onChange={handleChange}
                                                        className="form-control"
                                                        required
                                                        placeholder="Enter Contact No" maxLength="10"
                                                    pattern="\d{10}"
                                                    />
                                                </div>
                                            </div>
                                        </div>

                                        <div className="text-end">
                                            <button type="submit" className="btn btn-primary">
                                                Update Profile
                                            </button>
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>


                </div>
            </div>
        </div>
    );
}
