import React, { useState } from 'react';
import Sidebar from "../../Layout/Sidebar";
import Navbar from '../../Layout/Navbar';
import Footer from '../../Layout/Footer';
import { Endpoints } from "../../API/Endpoints";
import { CommonApi } from "../../API/CommonApi";
import ToastMessage from "../../Utils/ToastMessage";
import { useHistory } from "react-router-dom";

export default function ImportProduct() {
  const history = useHistory();

  const [formData, setFormData] = useState({
    file: null,       // CSV file
    zip_file: null,   // ZIP file
  });

  const [isLoading, setIsLoading] = useState(false);

  const handleChange = (e) => {
    const { name, files } = e.target;
    setFormData((prevState) => ({
      ...prevState,
      [name]: files[0],
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);

    const productForm = new FormData();
    productForm.append("file", formData.file);
    productForm.append("zip_file", formData.zip_file);

    try {
      const response = await CommonApi(Endpoints.ProductImport, productForm);
      if (response?.status === true) {
        ToastMessage.Success("Product imported successfully!");
        history.push("/product");
      } else {
        ToastMessage.Error(response?.message || "Failed to import product.");
      }
    } catch (error) {
      ToastMessage.Error(error?.response?.data?.message || "Something went wrong.");
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div class="page-wrapper">
       <Navbar />
       <Sidebar />
       <div class="page-wrapper">
         <div class="main-content">
               <div className="card">
                 <div className="card-header">
                   <h4 className="header-title">Create Product</h4>
                 </div>
                 <div className="card-body">
              
                   <form onSubmit={handleSubmit} encType="multipart/form-data">
                     <div className="row">
                       {/* Left Column */}
                       <div className="col-md-6">
                         <div className="mb-3">
                           <div className="d-flex justify-content-between">
                              <label className="form-label">CSV File</label>
                              <a href="product.xlsx">Download Sample CSV</a>
                           </div>
                          

                           <input type="file" name="file" className="form-control" onChange={handleChange} required />
                         </div>
                       </div>
                       <div className="col-md-6">
                         <div className="mb-3">
                            <div className="d-flex justify-content-between">
                              <label className="form-label">ZIP File (Images)</label>
                              <a href="image_zip.zip">Download Sample ZIP File (Images)</a>
                            </div>
                          
                           <input type="file" name="zip_file" className="form-control" onChange={handleChange} required />
                         </div>
                       </div>
   
                       <div className="mt-4">
                         <button type="submit" className="btn btn-primary" disabled={isLoading}>
                           {isLoading ? "Submitting..." : "Submit"}
                         </button>
                       </div>
                     </div>
                   </form>
                 </div>
               </div>
             
           </div>
           <Footer />
         </div>
       </div>
  )
}
