import React, { useState, useEffect } from 'react';
import Sidebar from "../../Layout/Sidebar";
import Navbar from '../../Layout/Navbar';
import Footer from '../../Layout/Footer';
import { Endpoints } from "../../API/Endpoints";
import { CommonApi } from "../../API/CommonApi";
import { FetchApi } from "../../API/FetchApi";
import ToastMessage from "../../Utils/ToastMessage";
import { useHistory } from "react-router-dom";
import Select from 'react-select';

export default function ProductAdd() {
  const history = useHistory();

  // State to hold form input values
  const [imagePreview, setImagePreview] = useState([]);
  const [formData, setFormData] = useState({
    image: [],
    video: null,
    sku_code: "",
    name: "",
    description: "",
    collection: "",
    category_id: "",
    size: "",
    qty: "",
  });

  // Category dropdown data
  const [categoryList, setCategoryList] = useState([]);
  const [collectionList, setCollectionList] = useState([]);
  const [fields, setFields] = useState([
    { type: "", value: [], options:[] }
  ]);

  const handleFieldChange = async(index, name, value)=> {
    const updatedFields = [...fields];
    if (name === "type") {
      try {
        const resp = await FetchApi(Endpoints.variationData+"/"+ value);
        if (resp && resp.status === true) {
          
          updatedFields[index] = {
            type: value,
            value: [], // reset value when type changes
            options: resp.data || [] // load options based on type
          };
          
        } else {
          ToastMessage.Error(resp.message || "Failed to fetch categories");
        }
      } catch (e) {
        ToastMessage.Error(e?.response?.data?.message || "Error loading categories");
      }
    }else if (name === "value") {
      updatedFields[index].value = value;
    }
  
    setFields(updatedFields);

  };

  const addField = () => {
    setFields([...fields, { type: "", value: [], options:[] }]);
  };

  // Loading state
  const [isLoading, setIsLoading] = useState(false);

  // Load categories on mount
  useEffect(() => {
    getCategoryList();
    getCollectionList();
  }, []);

const getCategoryList = async (collectionId) => {
  try {
    const resp = await FetchApi(Endpoints.CategoryCollection + "/" + collectionId);
    if (resp && resp.status === true) {
      setCategoryList(resp.data);
    } else {
      ToastMessage.Error(resp.message || "Failed to fetch categories");
    }
  } catch (e) {
    ToastMessage.Error(e?.response?.data?.message || "Error loading categories");
  }
};

  const getCollectionList = async () => {
    try {
      const resp = await FetchApi(Endpoints.Collection);
      if (resp && resp.status === true) {
        setCollectionList(resp.data);
      } else {
        ToastMessage.Error(resp.message || "Failed to fetch Collection");
      }
    } catch (e) {
      ToastMessage.Error(e?.response?.data?.message || "Error loading Collection");
    }
  };

  // Input change handler
const handleChange = (e) => {
    const { name, value, files } = e.target;
    if (name === "image") {
      const files = Array.from(e.target.files);

      setFormData({ ...formData, image: files });
      const newFiles = files.map(file => ({ file, url: URL.createObjectURL(file) }));
      setImagePreview(prev => [...prev, ...newFiles]);

    }else if (name === "video") {
      const videoFile = files[0];
      setFormData(prev => ({ ...prev, video: videoFile }));
  
    } else {
      setFormData(prev => ({ ...prev, [name]: value }));
    }
     if (name === "collection") {
      getCategoryList(value);  // pass selected collection_id
    }
  };


  // Form submit
  const handleSubmit = async (e) => {
    e.preventDefault();
    const finalData = fields.map(field => ({
      type: field.type,
      value: field.value.map(item => item.value) 
    }));

    const mergedValues = finalData.reduce((acc, field) => acc.concat(field.value), []);
   
    setIsLoading(true);

    const productForm = new FormData();
    productForm.append("sku_code", formData.sku_code);
    productForm.append("name", formData.name);
    productForm.append("description", formData.description);
    productForm.append("collection", formData.collection);
    productForm.append("category_id", formData.category_id);
    productForm.append("size", formData.size);
    productForm.append("qty", formData.qty);
    mergedValues?.forEach(value => {
      productForm.append("variation_id[]", value);
    });
    if (Array.isArray(formData.image)) {
      formData.image?.forEach((imageFile, index) => {
        productForm.append("image[]", imageFile);
      });
    }
    if (formData.video) {
      productForm.append("video", formData.video);
    }

    try {
      const response = await CommonApi(Endpoints.ProductStore, productForm);
      if (response && response.status === true) {
        ToastMessage.Success("Product created successfully!");
        history.push("/product"); // Navigate to product list
      } else {
        ToastMessage.Error(response?.message || "Failed to create product.");
      }
    } catch (error) {
      ToastMessage.Error(error?.response?.data?.message || "Error occurred while submitting.");
    } finally {
      setIsLoading(false);
    }
  };
  
  const removeField = (index) => {
    const updatedFields = fields.filter((_, i) => i !== index);
    setFields(updatedFields);
  };

  const handleRemoveImage = (index) => {
    setImagePreview(prev => prev.filter((_, i) => i !== index));
  };

  return (
    <div class="page-wrapper">
      <Navbar />
      <Sidebar />
      <div class="page-wrapper">
        <div class="main-content">
          <div className="card">
            <div className="card-header">
              <h4 className="header-title">Create Product</h4>
            </div>
            <div className="card-body">

              <form onSubmit={handleSubmit} encType="multipart/form-data">
                <div className="row">
                  {/* Left Column */}
                  
                  <div className="col-md-6">
                    <div className="mb-3">
                      <label className="form-label">Product Name</label>
                      <input type="text" name="name" className="form-control" onChange={handleChange} required />
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="mb-3">
                      <label className="form-label">SKU Code</label>
                      <input type="text" name="sku_code" className="form-control" onChange={handleChange} required />
                    </div>
                  </div>
                
                
                  <div className="col-md-6">
                    <div className="mb-3">
                      <label className="form-label">qty</label>
                      <input type="number" name="qty" className="form-control" onChange={handleChange} required />
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="mb-3">
                      <label className="form-label">Image</label>
                      <input type="file" name="image" className="form-control" 
                       accept="image/png, image/jpeg, image/jpg, image/gif, image/webp"
                      multiple  onChange={handleChange} required />
                    </div>
                  </div>
                  <div className="col-md-12">
                    <div className="row">
                        {imagePreview.map((img, index) => (
                          <div className="col-md-2 mb-3" key={index}>
                            <div className="card">
                              <img
                                src={URL.createObjectURL(img.file)}
                                alt="preview"
                                className="card-img-top"
                                style={{ height: "100px", objectFit: "cover" }}
                              />
                              <div className="card-body p-2">
                             
                                <button
                                  type="button"
                                  className="btn btn-sm btn-danger w-100"
                                  onClick={() => handleRemoveImage(index)}
                                >
                                  Remove
                                </button>
                              </div>
                            </div>
                          </div>
                        ))}
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="mb-3">
                      <label className="form-label">Video</label>
                      <input type="file" name="video" className="form-control"   onChange={handleChange} accept="video/*" required />
                    </div>
                  </div>
                  {formData.video && (
                      <div className="col-md-6">
                    <div className="mb-3">
                      {/* <label className="form-label">Video Preview</label> */}
                      <video
                        controls
                        width="300"
                        src={URL.createObjectURL(formData.video)}
                        style={{ borderRadius: "8px" }}
                      />
                    </div>
                    </div>
                  )}
                 
                  <div className="col-md-4">
                    <div className="mb-3">
                      <label className="form-label">Select collection</label>
                      <select name="collection" className="form-control" onChange={handleChange} required>
                        <option value="">Choose collection</option>
                        {collectionList.map((cat) => (
                          <option key={cat.id} value={cat.id}>{cat.name}</option>
                        ))}
                      </select>
                    </div>
                  </div>
                  <div className="col-md-4">
                    <div className="mb-3">
                      <label className="form-label">Select Category</label>
                      <select name="category_id" className="form-control" onChange={handleChange} required>
                        <option value="">Choose Category</option>
                        {categoryList.map((cat) => (
                          <option key={cat.id} value={cat.id}>{cat.name}</option>
                        ))}
                      </select>
                    </div>
                  </div>
                

                  {/* Right Column */}
                  <div className="col-md-4">
                    <div className="mb-3">
                      <label className="form-label">Select Size</label>
                      <select name="size" className="form-control" onChange={handleChange} required>
                        <option value="">Choose Size</option>
                        <option value="S">Small</option>
                        <option value="M">Medium</option>
                        <option value="L">Large</option>
                      </select>
                    </div>
                  </div>
                
                  
                  <div className="col-md-12">
                    <div className="mb-3">
                      <label className="form-label">Product description</label>
                      <textarea name="description" className="form-control" onChange={handleChange} required></textarea>
                    </div>
                  </div>

                  <div className="col-md-12">
                    {fields.map((field, index) => (
                      <div className="row mb-3" key={index}>
                        <div className="col-md-4">
                          <label className="form-label">Select Variation</label>
                          <select
                            name="type"
                            className="form-control"
                            value={field.type}
                            onChange={(e) => handleFieldChange(index, "type", e.target.value)}
                            required
                          >
                            <option value="" disabled>Select Type</option>
                        <option value="Color">Color</option>
                        <option value="Price">Price</option>
                        <option value="Fabric">Fabric</option>
                        <option value="Fit">Fit</option>
                        <option value="Neckline">Neckline</option>
                        <option value="Sleeve Type">Sleeve Type</option>
                        <option value="Waist Type">Waist Type</option>
                        <option value="Pattern">Pattern</option>
                          </select>
                        </div>

                        <div className="col-md-5">
                          <label className="form-label">Value</label>
                          <Select
                            isMulti
                            name="value"
                            options={field.options.map(opt => ({ value: opt.id, label: opt.value }))}
                            value={field.value.map(val => ({ value: val.value, label: val.label }))}
                            onChange={(selectedOptions) =>
                              handleFieldChange(index, "value", selectedOptions.map(option => option))
                            }
                            className="basic-multi-select"
                            classNamePrefix="select"  required
                          />

                         
                        </div>

                        <div className="col-md-3 d-flex align-items-end gap-2">
                          {index !== 0 && (
                            <button
                              type="button"
                              className="btn btn-secondary"
                              onClick={() => removeField(index)}
                            >
                              Remove
                            </button>
                          )}
                          {index === 0 && (
                            <button
                              type="button"
                              className="btn btn-primary"
                              onClick={addField}
                            >
                              + Add More
                            </button>
                          )}
                        </div>
                      </div>
                    ))}

                  </div>



                  <div className="mt-4">
                    <button type="submit" className="btn btn-primary" disabled={isLoading}>
                      {isLoading ? "Submitting..." : "Submit"}
                    </button>
                  </div>
                </div>
              </form>
            </div>
          </div>

        </div>
        <Footer />
      </div>
    </div>
  );
}
