import React, { useState, useEffect } from 'react';
import Sidebar from "../../Layout/Sidebar";
import Navbar from '../../Layout/Navbar';
import Footer from '../../Layout/Footer';
import { Endpoints } from "../../API/Endpoints";
import { CommonApi } from "../../API/CommonApi";
import { FetchApi } from "../../API/FetchApi";
import ToastMessage from "../../Utils/ToastMessage";
import { useHistory } from "react-router-dom";


export default function SocialLink() {
    const history = useHistory();

    const [SocialLinkData, setSocialLinkData] = useState({
        other: "",
        pinterest: "",
        twitter: "",
        whatsapp_number: "",
        facebook: "",
        youtube_channel: "",
        instagram: "",
    });

    const [isLoading, setIsLoading] = useState(false);

    const token = localStorage.getItem("token");

    // Handle field changes
    const handleChange = (e) => {
        const { name, value, files } = e.target;
        setSocialLinkData((prev) => ({
            ...prev,
            [name]: files ? files[0] : value,
        }));
    };

    // Handle form submit
    const handleSubmit = async (e) => {
        e.preventDefault();
        setIsLoading(true);

        const formData = new FormData();
        formData.append("instagram", SocialLinkData.instagram);
        formData.append("youtube_channel", SocialLinkData.youtube_channel);
        formData.append("facebook", SocialLinkData.facebook);
        formData.append("whatsapp_number", SocialLinkData.whatsapp_number);
        formData.append("twitter", SocialLinkData.twitter);
        formData.append("pinterest", SocialLinkData.pinterest);
        formData.append("other", SocialLinkData.other);

        try {
            const response = await CommonApi(Endpoints.Socialupdate, formData);
            if (response?.status === true) {
                ToastMessage.Success("Social link updated successfully!");
                window.location.reload();
            } else {
                ToastMessage.Error(response?.message || "Failed to update About Us.");
            }
        } catch (error) {
            ToastMessage.Error(error?.response?.data?.message || "Something went wrong.");
            console.error("Submit Error:", error);
        } finally {
            setIsLoading(false);
        }
    };

    // Fetch data on mount
    const getData = async () => {
        try {
            const response = await FetchApi(Endpoints.Socialgetdata, {
                headers: {
                    Authorization: `Bearer ${token}`,
                },
            });

            if (response?.status === true && response?.data) {
                const { other, pinterest, twitter, whatsapp_number, facebook, youtube_channel, instagram } = response.data;
                setSocialLinkData((prev) => ({
                    ...prev,
                    other,
                    pinterest,
                    twitter,
                    whatsapp_number,
                    facebook,
                    youtube_channel,
                    instagram
                }));
            }
        } catch (error) {
            if (error?.response?.status === 401) {
                ToastMessage.Error("Session expired. Please log in again.");
            } else {
                ToastMessage.Error(error?.response?.data?.message || "Error loading data.");
            }
            console.error("Fetch Error:", error);
        }
    };

    useEffect(() => {
        getData();
    }, []);

    return (
        <div class="page-wrapper">
            <Navbar />
            <Sidebar />
            <div class="page-wrapper">
                <div class="main-content">

                    <div className="row">
                        <div className="col-12">
                            <div className="card">
                                <div className="card-header">
                                    <h4 className="header-title">Social Link</h4>
                                </div>
                                <div className="card-body">

                                    <form onSubmit={handleSubmit} className="parsley-examples">
                                        <div className="row">
                                            <div className="col-md-6">
                                                <div className="mb-3">
                                                    <label className="form-label">Instagram</label>
                                                    <input
                                                        type="text"
                                                        name="instagram"
                                                        value={SocialLinkData.instagram}
                                                        onChange={handleChange}
                                                        className="form-control"
                                                        required
                                                        placeholder="Enter Instagram"
                                                    />
                                                </div>
                                            </div>
                                            <div className="col-md-6">
                                                <div className="mb-3">
                                                    <label className="form-label">Youtube Channel</label>
                                                    <input
                                                        type="text"
                                                        name="youtube_channel"
                                                        value={SocialLinkData.youtube_channel}
                                                        onChange={handleChange}
                                                        className="form-control"
                                                        required
                                                        placeholder="Enter Youtube Channel"
                                                    />
                                                </div>
                                            </div>
                                            <div className="col-md-6">
                                                <div className="mb-3">
                                                    <label className="form-label">Facebook</label>
                                                    <input
                                                        type="text"
                                                        name="facebook"
                                                        value={SocialLinkData.facebook}
                                                        onChange={handleChange}
                                                        className="form-control"
                                                        required
                                                        placeholder="Enter Facebook"
                                                    />
                                                </div>
                                            </div>
                                            <div className="col-md-6">
                                                <div className="mb-3">
                                                    <label className="form-label">Whatsapp Number</label>
                                                    <input
                                                        type="tel"
                                                        name="whatsapp_number"
                                                        value={SocialLinkData.whatsapp_number}
                                                        onChange={handleChange}
                                                        className="form-control"
                                                        required
                                                        placeholder="Enter Whatsapp Number"
                                                        maxLength="10"
                                                        pattern="\d{10}"
                                                    />
                                                </div>
                                            </div>
                                            <div className="col-md-6">
                                                <div className="mb-3">
                                                    <label className="form-label">X Link</label>
                                                    <input
                                                        type="text"
                                                        name="twitter"
                                                        value={SocialLinkData.twitter}
                                                        onChange={handleChange}
                                                        className="form-control"
                                                        required
                                                        placeholder="Enter X Link"
                                                    />
                                                </div>
                                            </div>
                                            <div className="col-md-6">
                                                <div className="mb-3">
                                                    <label className="form-label">Pinterest</label>
                                                    <input
                                                        type="text"
                                                        name="pinterest"
                                                        value={SocialLinkData.pinterest}
                                                        onChange={handleChange}
                                                        className="form-control"
                                                        required
                                                        placeholder="Enter Pinterest"
                                                    />
                                                </div>
                                            </div>
                                            <div className="col-md-6">
                                                <div className="mb-3">
                                                    <label className="form-label">Other</label>
                                                    <input
                                                        type="text"
                                                        name="other"
                                                        value={SocialLinkData.other}
                                                        onChange={handleChange}
                                                        className="form-control"
                                                        required
                                                        placeholder="Enter Other"
                                                    />
                                                </div>
                                            </div>

                                            <div className="mt-4">
                                                <button type="submit" className="btn btn-primary" disabled={isLoading}>
                                                    {isLoading ? (
                                                        <span>
                                                            <span
                                                                className="spinner-border spinner-border-sm"
                                                                role="status"
                                                                aria-hidden="true"
                                                            ></span>
                                                            &nbsp;Loading...
                                                        </span>
                                                    ) : (
                                                        "Submit"
                                                    )}
                                                </button>
                                            </div>
                                        </div>
                                    </form>
                                </div>
                            </div>

                        </div>
                    </div>
                </div>
                <Footer />
            </div>
        </div>
    );
}
