import React, { useState } from "react";
import { Endpoints } from "../../API/Endpoints";
import { CommonApi } from "../../API/CommonApi";
import ToastMessage from "../../Utils/ToastMessage";
import { Link, useHistory } from "react-router-dom/cjs/react-router-dom.min";

export default function Login() {
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [emailError, setEmailError] = useState("");
    const [passwordError, setPasswordError] = useState("");
    const [loading, setLoading] = useState(false);
    const [message, setMessage] = useState("");
    const Navigate = useHistory();

    const handleSubmit = async (e) => {
        e.preventDefault();
        setMessage("");
        let isValid = true;

        if (!email) {
            setEmailError("Please enter email");
            isValid = false;
        } else {
            setEmailError("");
        }

        if (!password) {
            setPasswordError("Please enter password");
            isValid = false;
        } else {
            setPasswordError("");
        }

        if (!isValid) return;

        setLoading(true);
        try {
            const response = await CommonApi(Endpoints.Login, { email, password });
            if (response?.status === true) {
                ToastMessage.Success(response.message);
                localStorage.setItem("token", response.token);
                localStorage.setItem("user", JSON.stringify(response.user));
                Navigate.push("/profile");
            } else {
                setMessage("User not found or incorrect credentials");
            }
        } catch (error) {
            setMessage(error.response?.data?.error || "User not found or incorrect credentials");
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className="splash-auth-wrapper">
            <div className="splash-auth-box">
                <div className="splash-auth-content">
                    <form onSubmit={handleSubmit}>
                        <Link to="/dashboard">
                            <img src="assets/logo.png" alt="Logo" className="w-50" />
                        </Link>
                        <h2>Hello, Welcome to <span>Quicklog Tab</span></h2>
                        <p>Fill the Details Below to Login to Your Account</p>
                        <div className="splash-auth-form">
                            <div className="splash-auth-feilds mb-30">
                                <input
                                    placeholder="Email Address"
                                    className="splash-input"
                                    type="email"
                                    id="emailaddress"
                                    value={email}
                                    onChange={(e) => setEmail(e.target.value)}
                                />
                                {emailError && <small className="text-danger">{emailError}</small>}
                                <div className="splash-auth-icon">
                                    {/* SVG here */}
                                </div>
                            </div>
                            <div className="splash-auth-feilds">
                                <input
                                    type="password"
                                    placeholder="Password"
                                    className="splash-input"
                                    value={password}
                                    onChange={(e) => setPassword(e.target.value)}
                                />
                                {passwordError && <small className="text-danger">{passwordError}</small>}
                                <div className="splash-auth-icon">
                                    {/* SVG here */}
                                </div>
                            </div>
                        </div>
                        {message && <div className="text-danger mb-2">{message}</div>}
                        <div className="splash-auth-btn">
                            <button
                                className="splash-btn splash-login-member w-100"
                                type="submit"
                                disabled={loading}
                            >
                                {loading ? "Logging in..." : "Log In"}
                            </button>
                        </div>
                    </form>
                    <p className="mt-3 text-muted">
                        Don't have an account?
                        <Link to="/register" className="mt-3 text-muted primary">
                            <b>Sign Up</b>
                        </Link>
                    </p>
                </div>
            </div>
        </div>
    );
}
