import React, { useState } from 'react';
import { useEffect } from 'react';
import Sidebar from "../../Layout/Sidebar";
import Navbar from '../../Layout/Navbar';
import Footer from '../../Layout/Footer';
import { Endpoints } from "../../API/Endpoints";
import { CommonApi } from "../../API/CommonApi";
import { FetchApi } from "../../API/FetchApi";
import ToastMessage from "../../Utils/ToastMessage";


export default function Domain() {
    const [domain, setDomain] = useState();
    const [IpAddress, setIpAddress] = useState();

    const [isLoading, setIsLoading] = useState(false);


    const handleSubmit = async (e) => {
        e.preventDefault();
        setIsLoading(true);

        const formData = new FormData();
        formData.append('domain_name', domain);

        try {
            const response = await CommonApi(Endpoints.DomainStore, formData);

            if (response && response.status === true) {
                ToastMessage.Success("Domain created successfully!");
                setIpAddress(response.ip_address);
                getData();
                // Optional: close modal via Bootstrap JS
                const modalEl = document.getElementById('domain-setup');
                const modal = window.bootstrap.Modal.getInstance(modalEl);
                modal.hide();

                const modalEl2 = document.getElementById('domain-connection');
                let modal2 = window.bootstrap.Modal.getInstance(modalEl2);
                
                if (!modal2) {
                    modal2 = new window.bootstrap.Modal(modalEl2);
                }
                
                modal2.show();

            } else {
                ToastMessage.Error(response?.message || "Failed to create domain.");
            }
        } catch (error) {
            ToastMessage.Error(error?.response?.message || "An error occurred while creating the domain.");
            console.error("Error:", error);
        } finally {
            setIsLoading(false);
        }
    };
    useEffect(() => {
        getData();
    }, []);

    const getData = async () => {
        try {
            let resp = await FetchApi(Endpoints.GetDomain);
            if (resp && resp.status === true) {
                setDomain(resp.data.domain_name);
                
            }
        } catch (e) {
            if (e && e.response && e.response.message) {
                ToastMessage.Error(e.response.message);
            }
        }
    };
    
    return (
        <div className="page-wrapper">
            <Navbar />
            <Sidebar />
            <div className="page-wrapper">
                <div className="main-content">
                    <div className="container-fluid">
                        <div className="row">
                            <div className="col-md-12">
                                <div className="card">
                                    <div className="card-header border-bottom py-1">
                                        <div className="d-flex justify-content-between">
                                            <h4 className="page-title">Domain</h4>
                                            <a href="#" className="text-right" data-bs-toggle="modal" data-bs-target="#domain-setup">Set up now</a>
                                        </div>
                                    </div>
                                    <div className="card-body">
                                        <div className="row">
                                            <div className="col-md-12">
                                                <div className="d-flex justify-content-between mb-2">
                                                    <p className="small mb-0">catalog.to Sub domain</p>
                                                </div>
                                             
                                                <p className="fw-bold" > {domain}</p>
                                         
                                            </div>
                                        </div>
                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
                <Footer />
            </div>

            {/* Modal for Domain Setup */}
            <div className="modal fade" id="domain-setup" tabIndex="-1" aria-labelledby="domain-setupLabel" aria-hidden="true">
                <div className="modal-dialog">
                    <div className="modal-content">
                        <form onSubmit={handleSubmit}>
                            <div className="modal-header">
                                <h5 className="modal-title" id="domain-setupLabel">Attach your Domain name to QuickLog</h5>
                                <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                            </div>
                            <div className="modal-body">
                                <div className="col-md-12">
                                    <div className="mb-3">
                                        <label className="form-label">Domain</label>
                                        <input
                                            type="text"
                                            name="domain_name"
                                            className="form-control"
                                            value={domain}
                                            onChange={(e)=>{
                                                setDomain(e.target.value)
                                            }}
                                            required
                                            placeholder="Type your domain"
                                        />
                                    </div>
                                </div>
                            </div>
                            <div className="modal-footer">
                                <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                                <button type="submit" className="btn btn-primary" disabled={isLoading}>
                                    {isLoading ? "Saving..." : "Save changes"}
                                </button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>

            <div className="modal fade" id="domain-connection" tabIndex="-1" aria-labelledby="domain-setupLabel" aria-hidden="true">
                <div className="modal-dialog modal-xl">
                    <div className="modal-content">
                           <div className="modal-header">
                                <h5 className="modal-title" id="domain-setupLabel">Domain connection instructions</h5>
                                <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                            </div>
                            <div className="modal-body">
                                <div className="col-md-12">
                                    <div className="mb-3">
                                        <h4>{domain}</h4><br />
                                        <p>Almost there!</p> 
                                        <p>A few more steps and you are</p> 
                                        <p>Ready to go</p>
                                      
                                        <p className='mt-4'>Add these DNS records to your domain</p>
                                            
                                        <p className='pt-1'>A</p>
                                        <p>Host : <span className="text-success">@</span></p>
                                        <p>Value : <span className="text-success">{IpAddress}</span></p>
                                    </div>
                                </div>
                            </div>
                            <div className="modal-footer">
                                <button type="button" className="btn btn-success" data-bs-dismiss="modal">Done</button>
                               
                            </div>
                        
                    </div>
                </div>
            </div>
        </div>
    );
}
