import React, { useState } from 'react';
import Sidebar from "../../Layout/Sidebar";
import Navbar from '../../Layout/Navbar';
import Footer from '../../Layout/Footer';
import { Endpoints } from "../../API/Endpoints";
import { CommonApi } from "../../API/CommonApi";
import ToastMessage from "../../Utils/ToastMessage";
import { useHistory } from "react-router-dom";

export default function BannerAdd() {
  const history = useHistory();

  const [bannerData, setBannerData] = useState({
    description: "",
    company_description: "",
    image: null
  });

  const [isLoading, setIsLoading] = useState(false);

  const handleChange = (e) => {
    const { name, value, files } = e.target;

    if (name === "image") {
      setBannerData({ ...bannerData, image: files[0] });
    } else {
      setBannerData({ ...bannerData, [name]: value });
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);

    const formData = new FormData();

    formData.append('image', bannerData.image);

    try {
      const response = await CommonApi(Endpoints.bannerstore, formData);

      if (response && response.status === true) {
        ToastMessage.Success("Banner created successfully!");
        console.log("Response:", response.data);

        setBannerData({

          image: null
        });

        history.push("/banner");
      } else {
        ToastMessage.Error(response?.message || "Failed to create banner.");
      }
    } catch (error) {
      ToastMessage.Error(error?.response?.data?.message || "An error occurred while creating the banner.");
      console.error("Error:", error);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div class="page-wrapper">
      <Navbar />
      <Sidebar />
      <div class="page-wrapper">
        <div class="main-content">
          <div className="row">
            <div className="col-12">
              <div className="card">
                <div className="card-header">
                  <h4 className="header-title">Create Banner</h4>
                </div>
                <div className="card-body">

                  <form onSubmit={handleSubmit} className="parsley-examples">
                    <div className="row">
                      <div className="col-md-12">
                        <div className="mb-3">
                          <label className="form-label">Banner Image</label>
                          <input
                            type="file"
                            name="image"
                            onChange={handleChange}
                            className="form-control"
                            required
                          />
                        </div>
                      </div>


                      <div className="mt-4">
                        <button type="submit" className="btn btn-primary" disabled={isLoading}>
                          {isLoading ? (
                            <span>
                              <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                              &nbsp;Loading...
                            </span>
                          ) : (
                            "Submit"
                          )}
                        </button>
                      </div>
                    </div>
                  </form>
                </div>
              </div>

            </div>
          </div>
        </div>
        <Footer />
      </div>
    </div>
  );
}
