import React, { useState, useEffect } from 'react';
import Sidebar from "../../Layout/Sidebar";
import Navbar from '../../Layout/Navbar';
import Footer from '../../Layout/Footer';
import { Endpoints } from "../../API/Endpoints";
import { CommonApi } from "../../API/CommonApi";
import { FetchApi } from "../../API/FetchApi";
import ToastMessage from "../../Utils/ToastMessage";
import { useHistory } from "react-router-dom";

export default function AboutUs() {
  const history = useHistory();
  const token = localStorage.getItem("token");

  const [categoryData, setCategoryData] = useState({
    company_description: "",
    description: "",
    banner: null,
    owner_image: null
  });

  const [previewImages, setPreviewImages] = useState({
    banner: null,
    owner_image: null,
  });

  const [isLoading, setIsLoading] = useState(false);

  const handleChange = (e) => {
    const { name, value, files } = e.target;

    if (files) {
      const file = files[0];
      setCategoryData((prev) => ({
        ...prev,
        [name]: file,
      }));

      if (file) {
        const previewUrl = URL.createObjectURL(file);
        setPreviewImages((prev) => ({
          ...prev,
          [name]: previewUrl,
        }));
      }
    } else {
      setCategoryData((prev) => ({
        ...prev,
        [name]: value,
      }));
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);

    const formData = new FormData();
    formData.append("company_description", categoryData.company_description);
    formData.append("description", categoryData.description);
    if (categoryData.banner) formData.append("banner", categoryData.banner);
    if (categoryData.owner_image) formData.append("owner_image", categoryData.owner_image);

    try {
      const response = await CommonApi(Endpoints.Aboutupdate, formData);
      if (response?.status === true) {
        ToastMessage.Success("About Us updated successfully!");
 
      } else {
        ToastMessage.Error(response?.message || "Failed to update About Us.");
      }
    } catch (error) {
      ToastMessage.Error(error?.response?.data?.message || "Something went wrong.");
      console.error("Submit Error:", error);
    } finally {
      setIsLoading(false);
    }
  };

  const getData = async () => {
    try {
      const response = await FetchApi(Endpoints.aboutgetdata, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });

      if (response?.status === true && response?.data) {
        const { company_description, description, banner, onwer_image } = response.data;

        setCategoryData((prev) => ({
          ...prev,
          company_description,
          description,
        }));

        setPreviewImages({
          banner: banner || null,
          onwer_image: onwer_image || null,
        });
      }
    } catch (error) {
      if (error?.response?.status === 401) {
        ToastMessage.Error("Session expired. Please log in again.");
      } else {
        ToastMessage.Error(error?.response?.data?.message || "Error loading data.");
      }
      console.error("Fetch Error:", error);
    }
  };

  useEffect(() => {
    getData();
  }, []);



  return (
    <div class="page-wrapper">
      <Navbar />
      <Sidebar />
      <div class="page-wrapper">
        <div class="main-content">

          <div className="row">
            <div className="col-12">
              <div className="card">
                <div className="card-header">
                  <h4 className="header-title">About Us</h4>
                </div>
                <div className="card-body">

                  <form onSubmit={handleSubmit} className="parsley-examples">
                    <div className="row">
                      <div className="col-md-6">
                        <div className="mb-3">
                          <label className="form-label">Banner Image</label>
                          <input
                            type="file"
                            name="banner"
                            onChange={handleChange}
                            className="form-control"
                          />
                        </div>
                      </div>
                      

                      <div className="col-md-6">
                      {previewImages.banner && (
                          <div className="mb-3">
                            <label className="form-label">Current Banner Preview</label>
                            <div>
                              <img
                                src={previewImages.banner}
                                alt="Banner Preview"
                                style={{ maxWidth: "200px", borderRadius: "5px" }}
                              />
                            </div>
                          </div>
                        )}
                        </div>
                      <div className="col-md-6">
                        <div className="mb-3">
                          <label className="form-label">Owner Photo</label>
                          <input
                            type="file"
                            name="owner_image"
                            onChange={handleChange}
                            className="form-control"
                          />
                        </div>
                      </div>
                      <div className="col-md-6">
                      {previewImages.owner_image && (
                          <div className="mb-3">
                            <label className="form-label">Current Owner Photo Preview</label>
                            <div>
                              <img
                                src={previewImages.owner_image}
                                alt="Owner Preview"
                                style={{ maxWidth: "200px", borderRadius: "5px" }}
                              />
                            </div>
                          </div>
                        )}
                        </div>

                      <div className="col-md-12">
                        <div className="mb-3">
                          <label className="form-label">Description</label>
                          <textarea
                            className="form-control"
                            name="description"
                            required
                            rows="4"
                            placeholder="Enter Description"
                            onChange={handleChange}
                            value={categoryData.description}
                          ></textarea>
                        </div>
                      </div>

                      <div className="col-md-12">
                        <div className="mb-3">
                          <label className="form-label">Company Description</label>
                          <textarea
                            className="form-control"
                            name="company_description"
                            required
                            rows="4"
                            placeholder="Enter Company Description"
                            onChange={handleChange}
                            value={categoryData.company_description}
                          ></textarea>
                        </div>
                      </div>

                      <div className="mt-4">
                        <button type="submit" className="btn btn-primary" disabled={isLoading}>
                          {isLoading ? (
                            <span>
                              <span
                                className="spinner-border spinner-border-sm"
                                role="status"
                                aria-hidden="true"
                              ></span>
                              &nbsp;Loading...
                            </span>
                          ) : (
                            "Submit"
                          )}
                        </button>
                      </div>
                    </div>
                  </form>
                </div>
              </div>

            </div>
          </div>
        </div>
        <Footer />
      </div>
    </div>
  );
}
