import React, { useState, useEffect } from 'react';
import Sidebar from "../../Layout/Sidebar";
import Navbar from '../../Layout/Navbar';
import Footer from '../../Layout/Footer';
import { Endpoints } from "../../API/Endpoints";
import { CommonApi } from "../../API/CommonApi";
import { FetchApi } from "../../API/FetchApi";
import ToastMessage from "../../Utils/ToastMessage";
import { useHistory, useLocation } from "react-router-dom";

export default function BannerEdit() {
  const history = useHistory();
  const location = useLocation();
  const item = location?.state?.bannerId;

  const [bannerData, setBannerData] = useState({
    id: "",
    description: "",
    company_description: "",
    image: null
  });

  const [isLoading, setIsLoading] = useState(false);
  const [imagePreview, setImagePreview] = useState(null);
  const [existingImageUrl, setExistingImageUrl] = useState(null); // to show current image from server

  const handleChange = (e) => {
    const { name, value, files } = e.target;
    if (name === "image") {
      const file = files[0];
      setBannerData({ ...bannerData, image: file });
      if (file) {
        setImagePreview(URL.createObjectURL(file));
      }
    } else {
      setBannerData({ ...bannerData, [name]: value });
    }
  };

  useEffect(() => {
    if (item) {
      getData();
    } else {
      ToastMessage.Error("Invalid banner ID.");
      history.push("/banner");
    }
  }, [item]);

  useEffect(() => {
    // Cleanup preview URL when component unmounts or new image is selected
    return () => {
      if (imagePreview) {
        URL.revokeObjectURL(imagePreview);
      }
    };
  }, [imagePreview]);

  const getData = async () => {
    try {
      const resp = await FetchApi(`${Endpoints.bannerDetails}/${item}`);
      if (resp && resp.status === true) {
        const { id, description, company_description, image } = resp.data;
        setBannerData({ id, description, company_description, image: null });
        setExistingImageUrl(image); // set current server image
      } else {
        ToastMessage.Error(resp?.message || "Failed to fetch banner details.");
      }
    } catch (e) {
      if (e?.response?.status === 401) {
        ToastMessage.Error("Session Expired! Please login again.");
      } else {
        ToastMessage.Error(e?.response?.data?.message || "Failed to load banner data.");
      }
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);

    const formData = new FormData();
    formData.append('id', bannerData.id);
    formData.append('description', bannerData.description);
    formData.append('company_description', bannerData.company_description);
    if (bannerData.image) {
      formData.append('image', bannerData.image);
    }

    try {
      const response = await CommonApi(Endpoints.bannerUpdate, formData);

      if (response && response.status === true) {
        ToastMessage.Success(response.message || "Banner updated successfully.");
        history.push("/banner");
      } else {
        ToastMessage.Error(response?.message || "Failed to update banner.");
      }
    } catch (error) {
      ToastMessage.Error(error?.response?.data?.message || "An error occurred while updating the banner.");
      console.error("Error:", error);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="page-wrapper">
      <Navbar />
      <Sidebar />
      <div className="page-wrapper">
        <div className="main-content">
          <div className="row">
            <div className="col-12">
              <div className="card">
                <div className="card-header">
                  <h4 className="header-title">Edit Banner</h4>
                </div>
                <div className="card-body">
                  <form onSubmit={handleSubmit} className="parsley-examples">
                    <div className="row">
                      <div className="col-md-6">
                        <div className="mb-3">
                          <label className="form-label">Banner Image</label>
                          <input
                            type="file"
                            name="image"
                            onChange={handleChange}
                            className="form-control"
                          />
                        </div>
                      </div>

                      {(imagePreview || existingImageUrl) && (
                        <div className="col-md-6">
                          <div className="mb-3">
                            <label className="form-label">Image Preview</label>
                            <div>
                              <img
                                src={imagePreview || existingImageUrl}
                                alt="Preview"
                                style={{ width: "150px", height: "auto", borderRadius: "5px" }}
                              />
                            </div>
                          </div>
                        </div>
                      )}

                      <div className="mt-4">
                        <button type="submit" className="btn btn-primary" disabled={isLoading}>
                          {isLoading ? (
                            <span>
                              <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                              &nbsp;Loading...
                            </span>
                          ) : (
                            "Submit"
                          )}
                        </button>
                      </div>
                    </div>
                  </form>
                </div>
              </div>

            </div>
          </div>
        </div>
        <Footer />
      </div>
    </div>
  );
}
