import React, { useEffect, useState } from "react";
import { Endpoints } from "../API/Endpoints";
import { CommonApi } from "../API/CommonApi";
import ToastMessage from "../Utils/ToastMessage";
import { Link, useHistory } from "react-router-dom";
import { FetchApi } from "../API/FetchApi";

export default function Navbar() {
  const Navigate = useHistory();
  const token = localStorage.getItem("token");
  const [domain, setDomain] = useState();

  const handleLogout = async () => {
    try {

      if (!token) {
        // No token found, just clear localStorage and redirect
        localStorage.removeItem("token");
        localStorage.removeItem("user");
        Navigate.push("/");  // Redirect to the login page
        return;
      }

      // Send logout request to API if token exists
      const response = await CommonApi(Endpoints.Logout, token);

      if (response && response.status === true) {
        ToastMessage.Success(response.message);
      } else {
        ToastMessage.Error(response.message || "Logout failed.");
      }
    } catch (error) {
      ToastMessage.Error(error.response?.data?.message || "Logout failed.");
    }

    // Clear user session
    localStorage.removeItem("token");
    localStorage.removeItem("user");

    // Redirect to login after successful logout
    Navigate.push("/");
  };
  useEffect(() => {
    if (!token) {
      // No token found, just clear localStorage and redirect
      localStorage.removeItem("token");
      localStorage.removeItem("user");
      Navigate.push("/");  // Redirect to the login page
    }
    getData();
  }, [])

  const getData = async () => {
      try {
          let resp = await FetchApi(Endpoints.GetDomain);
          if (resp && resp.status === true) {
              setDomain(resp.data.domain_name);
              
          }
      } catch (e) {
          if (e && e.response && e.response.message) {
              ToastMessage.Error(e.response.message);
          }
      }
  };
  const goToPage= ()=>{
    let Newdomain = domain; // or dynamic domain
    // Check if domain does not already start with 'http://' or 'https://'
    if (!/^https?:\/\//i.test(Newdomain)) {
      Newdomain = "https://" + Newdomain; // Add 'https://' if missing
    }
    
    window.location.href = Newdomain;
  };
  return (
    <header class="header-wrapper main-header">
      <div class="header-inner-wrapper">
        <div class="header-right">

          <div class="header-left">
            <div class="header-links">
              <a href="javascript:void(0);" class="toggle-btn">
                <span></span>
              </a>

            </div>

            <div class="">
              <button onClick={goToPage} className="btn btn-light border-0 w-100" style={{ borderRadius: "0" }}>
                <span>View store</span>
              </button>
            </div>
          </div>
          <div class="header-controls">
            <div class="user-info-wrapper header-links">
              <a href="javascript:void(0);" class="user-info">
                <img src="assets/images/user.jpg" alt="" class="user-img" />
                <div class="blink-animation">
                  <span class="blink-circle"></span>
                  <span class="main-circle"></span>
                </div>
              </a>
              <div class="user-info-box">

                <ul>

                  <li>
                    <a href="javascript:void(0);" onClick={() => {
                      handleLogout()
                    }}>
                      <i class="fas fa-sign-out-alt"></i> logout
                    </a>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </header>
  )
}
