import React, { useEffect, useState } from 'react'
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';
// import required modules
import { Autoplay, Pagination, Navigation } from 'swiper/modules';
import { Endpoints } from "../API/Endpoints";
import { FetchApi } from "../API/FetchApi";
import { CommonApi } from "../API/CommonApi";
import ToastMessage from "../Utils/ToastMessage";

export default function ViewStore() {
    const user = JSON.parse(localStorage.getItem("user"));
    const [profileData, setProfileData] = useState();
    const [socialData, setSocialData] = useState();
    const [aboutata, setAboutData] = useState();
    const [banner, setBanner] = useState([]);
    const [product, setProduct] = useState([]);
    const [collection, setCollection] = useState([]);
    const [category, setCategory] = useState([]);
    const [noDomain, setnoDomain] = useState(false);
    useEffect(() => {
        getData();
    }, []);
    const getData = async () => {

        let data ={
            domain:window.location.origin
        }
        try {
            const response = await CommonApi(Endpoints.ViewStore, data);
            if (response?.status === true) {
                setProfileData(response?.Profile);
                setSocialData(response?.SocialLink);
                setBanner(response?.Banner);
                setProduct(response?.Product);
                setCollection(response?.Collection);
                setAboutData(response?.About);
                setCategory(response?.Category);
            } else {
                setnoDomain(true);
                // ToastMessage.Error(response?.message);
            }
        } catch (error) {
            if (error?.response?.status === 401) {
                ToastMessage.Error("Session expired. Please log in again.");
            } else {
                ToastMessage.Error(error?.response?.data?.message || "Error loading data.");
            }
            console.error("Fetch Error:", error);
        }
    };

    return (
        <>
            {noDomain == true ? 
               <div class="d-flex justify-content-center align-items-center vh-100">
               <img src="9-404-error-illustration-512x227-mli6zfam.png" alt="Error Image" class="img-fluid" />
             </div>
            : 
            <>
            <div class="container-fluid bg-white text-white py-2">
                <div class="row align-items-center">
                    <div class="col-md-2 text-center">
                        <div class="">
                            <img
                                src={profileData?.image ? profileData?.image : "assets/logo.png"}
                                alt="QuickSell"
                                width="150"
                                height="120"
                                className="me-2"
                            />
                        </div>
                    </div>
                    <div class="col-md-10 text-center">
                        <div class="row">
                            <div class="col-md-2">
                                <div class="text-center text-dark bold h4 py-2">{profileData?.brand_name}</div>
                            </div>
                            <div class="col-md-8">
                                <input type="text" class="form-control" placeholder="Search Bar" />

                            </div>
                            <div class="col-md-2 text-center">
                                <div class="py-2">
                                    <div className="d-flex gap-3">
                                        {socialData?.facebook ?
                                            <a href={socialData?.facebook} target="_blank" rel="noopener noreferrer" className="text-dark">
                                                <i className="fab fa-facebook-f"></i>
                                            </a>
                                            : ""}
                                        {socialData?.twitter ?
                                            <a href={socialData?.twitter} target="_blank" rel="noopener noreferrer" className="text-dark">
                                                <i className="fab fa-twitter"></i>
                                            </a>
                                            : ""}
                                        {socialData?.instagram ?
                                            <a href={socialData?.instagram} target="_blank" rel="noopener noreferrer" className="text-dark">
                                                <i className="fab fa-instagram"></i>
                                            </a>
                                            : ""}
                                        {socialData?.pinterest ?
                                            <a href={socialData?.pinterest} target="_blank" rel="noopener noreferrer" className="text-dark">
                                                <i className="fab fa-pinterest"></i>
                                            </a>
                                            : ""}
                                        {socialData?.youtube_channel ?
                                            <a href={socialData?.youtube_channel} target="_blank" rel="noopener noreferrer" className="text-dark">
                                                <i className="fab fa-youtube"></i>
                                            </a>
                                            : ""}
                                    </div>

                                </div>

                            </div>
                            <div class="col-md-10">
                                <nav class="navbar navbar-expand-lg navbar-dark bg-white text-dark">
                                    <ul class="navbar-nav  mb-2 mb-lg-0">
                                        <li class="nav-item px-3">
                                            <a class="nav-link text-dark" href="#">ABOUT US</a>
                                        </li>
                                        <li class="nav-item dropdown px-3">
                                            <a class="nav-link text-dark dropdown-toggle" href="#" id="categoriesDropdown" role="button" data-bs-toggle="dropdown">
                                                CATEGORIES
                                            </a>
                                            <ul class="dropdown-menu">
                                                {category.map((item, index) => {
                                                    return (
                                                        <li key={index}><a class="dropdown-item" href="#">{item.name}</a></li>
                                                    )
                                                })}
                                            </ul>
                                        </li>
                                        <li class="nav-item dropdown px-3">
                                            <a class="nav-link text-dark dropdown-toggle" href="#" id="collectionsDropdown" role="button" data-bs-toggle="dropdown">
                                                COLLECTION
                                            </a>
                                            <ul class="dropdown-menu">
                                                {collection.map((item, index) => {
                                                    return (
                                                        <li key={index}><a class="dropdown-item" href="#">{item.name}</a></li>
                                                    )
                                                })}
                                            </ul>
                                        </li>
                                        <li class="nav-item text-dark dropdown px-3">
                                            <a class="nav-link text-dark dropdown-toggle" href="#" id="productsDropdown" role="button" data-bs-toggle="dropdown">
                                                ALL PRODUCTS
                                            </a>
                                            <ul class="dropdown-menu">
                                                {product.map((item, index) => {
                                                    return (
                                                        <li key={index}><a class="dropdown-item" href="#">{item.name}</a></li>
                                                    )
                                                })}
                                            </ul>
                                        </li>
                                        <li class="nav-item px-3">
                                            <a class="nav-link text-dark" href="#">CONTACT US</a>
                                        </li>
                                    </ul>
                                </nav>
                            </div>

                            <div class="col-md-2 text-center">

                                <div className="cart-icon position-relative mt-4 mx-0 px-0">
                                    <i className="fas fa-shopping-cart fa-lg text-dark"></i>

                                </div>
                            </div>
                        </div>
                    </div>

                </div>
            </div>

            <div class="mb-1">
                <Swiper
                    slidesPerView={1}
                    spaceBetween={30}
                    loop={true}
                    pagination={{
                        clickable: true,
                    }}
                    autoplay={{
                        delay: 2000,
                        disableOnInteraction: false,
                    }}
                    navigation={true}
                    modules={[Autoplay, Pagination, Navigation]}
                    className="mySwiper"
                >
                    {banner.map((item, index) => {
                        return (
                            <SwiperSlide key={index}><img src={item?.image ? item?.image : 'assets/images/slider/1.png'} alt={item?.name} className="w-100" /></SwiperSlide>
                        )
                    })}
                </Swiper>
            </div>


            <div class="mb-2">
                <div class="card">
                    <div class="card-body">
                        <Swiper
                            slidesPerView={6}
                            spaceBetween={30}
                            loop={true}
                            autoplay={{
                                delay: 2500,
                                disableOnInteraction: false,
                            }}
                            pagination={false}
                            navigation={true}
                            modules={[Autoplay, Pagination, Navigation]}
                            className="mySwiper"
                        >
                            {collection.map((item, index) => {
                                return (
                                    <SwiperSlide key={index}><img src={item?.image ? item?.image : 'assets/images/slider/1.png'} alt={item?.name} class="view-store-img" /></SwiperSlide>
                                )
                            })}

                        </Swiper>
                    </div>
                </div>
            </div>

            <div class="card mb-0">
                <div class="card-body">
                    <Swiper
                        slidesPerView={6}
                        spaceBetween={30}
                        loop={true}
                        autoplay={{
                            delay: 2500,
                            disableOnInteraction: false,
                        }}
                        pagination={false}
                        navigation={true}
                        modules={[Autoplay, Pagination, Navigation]}
                        className="mySwiper"
                    >
                        {category.map((item, index) => {
                            return (
                                <SwiperSlide key={index}><img src={item?.image ? item?.image : 'assets/images/slider/1.png'} alt={item?.name} class="view-store-img" /></SwiperSlide>
                            )
                        })}
                    </Swiper>
                </div>
            </div>

            <div class="mb-0">
                <Swiper
                    slidesPerView={1}
                    spaceBetween={30}
                    loop={true}
                    pagination={{
                        clickable: true,
                    }}
                    autoplay={{
                        delay: 2000,
                        disableOnInteraction: false,
                    }}
                    navigation={true}
                    modules={[Autoplay, Pagination, Navigation]}
                    className="mySwiper"
                >
                    {banner.map((item, index) => {
                        return (
                            <SwiperSlide key={index}><img src={item?.image ? item?.image : 'assets/images/slider/1.png'} alt={item?.name} className="w-100" /></SwiperSlide>
                        )
                    })}
                </Swiper>
            </div>

            <div class="mb-3 mt-3">
                <Swiper
                    slidesPerView={4}
                    spaceBetween={20}
                    loop={true}
                    autoplay={{
                        delay: 2500,
                        disableOnInteraction: false,
                    }}
                    pagination={false}
                    navigation={true}
                    modules={[Autoplay, Pagination, Navigation]}
                    className="mySwiper"
                >
                    {product.map((item, index) => {
                        return (
                            <SwiperSlide>
                                <div class="card p-1">
                                    <div class="card-body m-1 p-1">
                                        <div class="">
                                            <div class="product-item w-100">
                                                <img src={item?.singal_image ? item?.singal_image : "assets/images/slider/2.png"} class="w-100" alt="product-img" />

                                                <div class="product-ovr-links">
                                                    <ul>
                                                        <li>
                                                            <a href="product-single.html">
                                                                <svg
                                                                    xmlns="http://www.w3.org/2000/svg"
                                                                    width="17px" height="16px">
                                                                    <path fill-rule="evenodd" fill="rgb(255, 255, 255)"
                                                                        d="M16.949,4.171 L14.077,11.019 C13.972,11.258 13.752,11.410 13.501,11.410 L5.608,11.410 C5.325,11.410 5.073,11.215 5.000,10.932 L2.432,1.301 L0.629,1.301 C0.283,1.301 -0.000,1.008 -0.000,0.650 C-0.000,0.291 0.283,-0.002 0.629,-0.002 L2.903,-0.002 C3.186,-0.002 3.438,0.193 3.511,0.476 L6.079,10.107 L13.081,10.107 L15.408,4.562 L7.652,4.562 C7.306,4.562 7.023,4.269 7.023,3.910 C7.023,3.552 7.306,3.259 7.652,3.259 L16.373,3.259 C16.582,3.259 16.781,3.366 16.897,3.552 C17.012,3.737 17.033,3.964 16.949,4.171 ZM5.010,13.040 C5.796,13.040 6.436,13.704 6.436,14.519 C6.436,15.334 5.796,15.998 5.010,15.998 C4.224,15.998 3.585,15.334 3.585,14.519 C3.585,13.704 4.224,13.040 5.010,13.040 ZM13.825,13.040 C14.612,12.987 15.293,13.606 15.345,14.410 C15.366,14.813 15.251,15.192 15.000,15.486 C14.748,15.791 14.402,15.965 14.025,15.998 C13.993,15.998 13.951,15.998 13.920,15.998 C13.176,15.998 12.557,15.389 12.505,14.617 C12.452,13.813 13.039,13.095 13.825,13.040 Z" />
                                                                </svg>
                                                            </a>
                                                        </li>


                                                    </ul>
                                                </div>
                                            </div>
                                            <div class="product-text-rs">

                                                <h3><a href="product-single.html">{item.name}</a></h3>
                                                <h6>{item?.category_info?.name}</h6>

                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </SwiperSlide>
                        )
                    })}



                </Swiper>
            </div>

            <div class="card mb-0">
                <div class="card-body">

                    <div class="row">
                        <div class="col-md-4"><img src={aboutata?.onwer_image ? aboutata?.onwer_image : 'assets/images/slider/2.png'} alt='' /></div>
                        <div class="col-md-8">
                            <p>{aboutata?.description}</p>
                        </div>

                    </div>
                </div>
            </div>

            <footer className="bg-light py-4 mt-3">
                <div className="container">
                    <div className="row align-items-center">

                        {/* Left: Contact Information */}
                        <div className="col-md-4 mb-3 mb-md-0">
                            <h6 className="text-primary fw-bold">CONTACT US</h6>
                            <p className="mb-1">
                                <strong>Phone</strong><br />
                                <a href={"tel:+91" + profileData?.mobile} className="text-decoration-none text-dark">
                                    {profileData?.mobile}
                                </a>
                            </p>
                            <p className="mb-0">
                                <strong>Email</strong><br />
                                <a href={"mailto:" + profileData?.email} className="text-decoration-none text-dark">
                                    hxipatna@gmail.com
                                </a>
                            </p>
                        </div>

                        {/* Right: Powered by QuickSell */}
                        <div className="col-md-4 text-md-center">
                            <span className="me-2">powered by</span>
                            <a href="#" >
                                <img
                                    src={profileData?.image ? profileData?.image : "assets/logo.png"}
                                    alt="QuickSell"
                                    width="85"
                                    height="85"
                                    className="me-2"
                                />

                            </a>
                        </div>

                        <div className="col-md-4 mb-3 mb-md-0">

                            <p className="mb-1">
                                <strong>Address: </strong>
                                <span>{profileData?.address} </span>
                            </p>
                            <p className="mb-0">
                                <strong>GST: </strong>
                                <span>{profileData?.gst} </span>
                            </p>
                        </div>

                    </div>
                </div>

                {/* WhatsApp Floating Button */}
                {/* <a
                    href="https://wa.me/919431258969"
                    target="_blank"
                    rel="noopener noreferrer"
                    className="btn btn-success position-fixed"
                    style={{
                        bottom: "20px",
                        right: "20px",
                        borderRadius: "50px",
                        boxShadow: "0 0 10px rgba(0,0,0,0.15)"
                    }}
                >
                    <i className="fab fa-whatsapp" style={{ size:"48px"}}></i> 
                </a> */}
            </footer>
            </>
        }
        </>
    )
}
