import Swal from "sweetalert2";
import './ToastMessage.css'

function Message({ type, text, title = false }) {
    if (type == "Success") {
        Swal.fire({
            title: "Good job!",
            text: text,
            icon: "success"
        })

    }


    if (type == "Error") {
        Swal.fire({
            icon: "error",
            title: title,
            text: text,
        })
    }

    if (type == "Info") {
        Swal.fire({
            title: title,
            text: text,
            icon: 'info',
        })
    }
    // alert(message);
}
export default {
    Success: (text) => Message({ type: 'Success', text }),
    Error: (text) => Message({ type: 'Error', text }),
    Info: (text) => Message({ type: 'Info', text }),
   
}

