import React, { useState } from 'react';
import Sidebar from "../../Layout/Sidebar";
import Navbar from '../../Layout/Navbar';
import Footer from '../../Layout/Footer';
import { Endpoints } from "../../API/Endpoints";
import { CommonApi } from "../../API/CommonApi";
import ToastMessage from "../../Utils/ToastMessage";
import { useHistory } from "react-router-dom";

export default function VariationAdd() {
  const Navigate = useHistory();

  // State to store form data
  const [VariationData, setVariationData] = useState({
    type: "",
    value: "",
  });

  // State to handle the loading state of the submit button
  const [isLoading, setIsLoading] = useState(false);

  // Handle changes in form inputs
  const handleChange = (e) => {
    const { name, value } = e.target;
    setVariationData((prevState) => ({
      ...prevState,
      [name]: value,
    }));
  };

  // Handle form submission
  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);

    const formData = new FormData();
    formData.append('type', VariationData.type);
    formData.append('value', VariationData.value);

    try {
      const response = await CommonApi(Endpoints.variationtore, formData); // check if this is the correct endpoint name

      if (response && response.status === true) {
        ToastMessage.Success("Variation created successfully!");
        console.log("Response:", response.data);

        setVariationData({ type: "", value: "" });
        Navigate.push("/variation");
      } else {
        ToastMessage.Error(response?.message || "Failed to create variation.");
      }
    } catch (error) {
      ToastMessage.Error(error?.response?.data?.message || "An error occurred while creating the variation.");
      console.error("Error:", error);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div class="page-wrapper">
      <Navbar />
      <Sidebar />
      <div class="page-wrapper">
        <div class="main-content">
          <div className="row">
            <div className="col-12">
             
                <div className="card">
                  <div className="card-header">
                    <h4 className="header-title">Create Variation</h4>
                  </div>
                  <div className="card-body">

                    <form onSubmit={handleSubmit} className="parsley-examples">
                      <div className="row">

                        <div className="col-md-6">
                        <div className="mb-3">
                        <label className="form-label">Select Type</label>
                        <select name="type" className="form-control"value={VariationData.type}
                          onChange={handleChange} required>
                        <option value="" disabled selected>Select Type</option>
                        <option value="Color">Color</option>
                        <option value="Price">Price</option>
                        <option value="Fabric">Fabric</option>
                        <option value="Fit">Fit</option>
                        <option value="Neckline">Neckline</option>
                        <option value="Sleeve Type">Sleeve Type</option>
                        <option value="Waist Type">Waist Type</option>
                        <option value="Pattern">Pattern</option>
                        </select>

                      </div>
                        </div>

                        <div className="col-md-6">
                          <div className="mb-3">
                            <label className="form-label">Value</label>
                            <input
                              type="text"
                              name="value"
                          value={VariationData.value}
                          onChange={handleChange}
                              className="form-control"
                              required
                              placeholder="Enter value"
                            />
                          </div>
                        </div>


                        <div className="mt-4">
                          <button type="submit" className="btn btn-primary" disabled={isLoading}>
                            {isLoading ? (
                              <span>
                                <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                                &nbsp;Loading...
                              </span>
                            ) : (
                              "Submit"
                            )}
                          </button>
                          {/* Optionally you can add a reset button */}
                        </div>
                      </div>
                    </form>
                  </div>
                </div>
             
            </div>
          </div>
        </div>
        <Footer />
      </div>
    </div>
  );
}
