import React, { useState, useEffect } from 'react';
import Sidebar from "../../Layout/Sidebar";
import Navbar from '../../Layout/Navbar';
import Footer from '../../Layout/Footer';
import { Endpoints } from "../../API/Endpoints";
import { CommonApi } from "../../API/CommonApi";
import { FetchApi } from "../../API/FetchApi";
import ToastMessage from "../../Utils/ToastMessage";
import { useHistory, useLocation } from "react-router-dom";
export default function CollectionEdit() {
  const Navigate = useHistory();
   const location = useLocation();
   const item = location.state.collectionId;
 
   // State to store form data
   const [collectionData, setcollectionData] = useState({
     id: "",
     name: "",
     description: "",
     image: null
   });
 
   // State to handle the loading state of the submit button
   const [isLoading, setIsLoading] = useState(false);
   const [imagePreview, setImagePreview] = useState(null);
 
   // Handle changes in form inputs
   const handleChange = (e) => {
     const { name, value, files } = e.target;
     if (name === "image") {
       setcollectionData({ ...collectionData, image: files[0] });
     } else {
       setcollectionData({ ...collectionData, [name]: value });
     }
     if (name === "image") {
      const file = files[0];
      setcollectionData({ ...collectionData, image: file });
      if (file) {
        setImagePreview(URL.createObjectURL(file));
      } else {
        setImagePreview(null);
      }
    }
   };
 
   useEffect(() => {
     getData();
   }, [item]);
 
   const getData = async () => {
     try {
       let resp = await FetchApi(Endpoints.CollectionDetails + "/" + item);
       if (resp && resp.status == true) {
         const { image, ...restData } = resp.data;
         setcollectionData({ ...restData, image: null });
         if (image) {
          setImagePreview(image); // assuming `image` is a full URL
        }
       } else {
         ToastMessage.Error(resp.message);
       }
     } catch (e) {
       if (e && e.response && e.response.data && e.response.data.message) {
         if (e.response.status === 401) {
           ToastMessage.Error("Session Expired! Please login again.");
         } else {
           ToastMessage.Error(e.response.data.message);
         }
       }
     }
 
   };
 
   // Handle form submission
   const handleSubmit = async (e) => {
     e.preventDefault();
 
     // Set loading to true to show the loading state on the button
     setIsLoading(true);
 
     // Creating FormData object to send image and text data
     const formData = new FormData();
     formData.append('id', collectionData.id);
     formData.append('name', collectionData.name);
     formData.append('description', collectionData.description);
     if (collectionData.image) {
       formData.append('image', collectionData.image);
     }
 
 
     try {
       const response = await CommonApi(Endpoints.CollectionUpdate, formData);
 
       if (response && response.status === true) {
         ToastMessage.Success(response.message);
         // Redirect after successful collection creation
         Navigate.push("/collection");
       } else {
         ToastMessage.Error(response?.message || "Failed to create collection.");
       }
     } catch (error) {
       ToastMessage.Error(error?.response?.message || "An error occurred while creating the collection.");
       console.error("Error:", error);
     } finally {
       // Set loading to false after the API call is complete (success or error)
       setIsLoading(false);
     }
   };
 
   return (
     <div class="page-wrapper">
       <Navbar />
       <Sidebar />
       <div class="page-wrapper">
         <div class="main-content">
           <div className="row">
             <div className="col-12">
                 <div className="card">
                   <div className="card-header">
                     <h4 className="header-title">Edit collection</h4>
                   </div>
                   <div className="card-body">
 
                     <form onSubmit={handleSubmit} className="parsley-examples">
                       <div className="row">
                         <div className="col-md-12">
                           <div className="mb-3">
                             <label className="form-label">Name</label>
                             <input
                               type="text"
                               name="name"
                               value={collectionData.name}
                               onChange={handleChange}
                               className="form-control"
                               required
                               placeholder="Enter Name"
                             />
                           </div>
                         </div>
 
                         <div className="col-md-6">
                           <div className="mb-3">
                             <label className="form-label">collection Image</label>
                             <input
                               type="file"
                               name="image"
                               onChange={handleChange}
                               className="form-control"
 
                             />
                           </div>
                           </div>
                           {imagePreview && (
                        <div className="col-md-6">

                          <div className="mb-3">
                            <label className="form-label">Image Preview</label>
                            <div>
                              <img src={imagePreview} alt="Preview" style={{ width: "150px", height: "auto", borderRadius: "5px" }} />
                            </div>
                          </div>
                        </div>
                      )}
                        
                         <div className="col-md-12">
                           <div className="mb-3">
                             <label className="form-label">Description</label>
                             <textarea className="form-control"
                               name="description"
                               required
                               rows="4"
                               cols="50"
                               placeholder="Enter Description"
                               onChange={handleChange}
                               value={collectionData.description}></textarea>
 
                           </div>
                         </div>
 
                         <div className="mt-4">
                           <button type="submit" className="btn btn-primary" disabled={isLoading}>
                             {isLoading ? (
                               <span>
                                 <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                                 &nbsp;Loading...
                               </span>
                             ) : (
                               "Submit"
                             )}
                           </button>
                           {/* Optionally you can add a reset button */}
                         </div>
                       </div>
                     </form>
                   </div>
                 </div>
              
             </div>
           </div>
         </div>
         <Footer />
       </div>
     </div>
   );
 }
