
import { BrowserRouter , Route, Switch } from 'react-router-dom';
import Home from './component/Home';
import Login from './component/Auth/Login';
import Register from './component/Auth/Register';
import Category from './component/Category/Category';
import CategoryAdd from './component/Category/CategoryAdd';
import CategoryEdit from './component/Category/CategoryEdit';
import Product from './component/Product/Product';
import ProductAdd from './component/Product/ProductAdd';
import ProductEdit from './component/Product/ProductEdit';
import Banner from './component/Banner/Banner';
import BannerAdd from './component/Banner/BannerAdd';
import BannerEdit from './component/Banner/BannerEdit';
import AboutUs from './component/AboutUs/AboutUs';
import HeaderAndFooter from './component/AboutUs/HeaderAndFooter';
import SocialLink from './component/SocialLink/SocialLink';
import Domain from './component/SocialLink/Domain';
import CollectionAdd from './component/Collection/CollectionAdd';
import CollectionEdit from './component/Collection/CollectionEdit';
import Collection from './component/Collection/Collection';
import ImportProduct from './component/Product/ImportProduct';
import Variation from './component/Variation/Variation';
import VariationAdd from './component/Variation/VariationAdd';
import VariationEdit from './component/Variation/VariationEdit';
import ViewStore from './component/ViewStore';
import RemoveProduct from './component/Product/RemoveProduct';
import ArchiveProduct from './component/Product/ArchiveProduct';
import DynamicCSSLoader from "./Layout/DynamicCSSLoader";
import Landing from './component/Landing';
import DomainMainCssLoader from './Layout/DomainMainCssLoader';

function App() {  
  const baseName = window.location.origin;
  console.log("baseName", baseName);
  console.log("process.env.REACT_APP_BASE_URL", process.env.REACT_APP_BASE_URL);
  if(baseName == process.env.REACT_APP_BASE_URL){
    return (
      <BrowserRouter>
        <DynamicCSSLoader />
        <Switch>
          <Route exact path="/profile" >
              <Home />
          </Route>
          <Route exact path="/">  
            <Landing />
          </Route> 

          <Route exact path="/login">  
            <Login />
          </Route> 
          <Route exact path="/register">  
            <Register />
          </Route> 
          <Route exact path="/category">  
            <Category />
          </Route> 
          <Route exact path="/add-category">  
            <CategoryAdd />
          </Route> 
  
          <Route exact path="/edit-category">  
            <CategoryEdit />
          </Route> 
          <Route exact path="/product">  
            <Product />
          </Route> 
          <Route exact path="/remove-product">  
            <RemoveProduct />
          </Route> 
          <Route exact path="/archive-product">  
            <ArchiveProduct />
          </Route> 
          <Route exact path="/create-product">  
            <ProductAdd />
          </Route> 
          <Route exact path="/edit-product">  
            <ProductEdit />
          </Route> 
          <Route exact path="/import-product">  
            <ImportProduct />
          </Route> 
        
          <Route exact path="/banner">  
            < Banner />
          </Route> 
          <Route exact path="/add-banner">  
            <BannerAdd />
          </Route> 
  
          <Route exact path="/edit-banner">  
            <BannerEdit />
            </Route>
          <Route exact path="/about-us">  
            <AboutUs />
            </Route>
          <Route exact path="/header-footer">  
            <HeaderAndFooter />
            </Route>
          <Route exact path="/social-link">  
            <SocialLink />
            </Route>
          <Route exact path="/domain">  
            <Domain />
            </Route>
  
            <Route exact path="/collection">  
            < Collection />
          </Route> 
          <Route exact path="/add-collection">  
            <CollectionAdd />
          </Route> 
  
          <Route exact path="/edit-collection">  
            <CollectionEdit />
          </Route>
          <Route exact path="/variation">  
            <Variation />
          </Route>
          <Route exact path="/add-variation">  
            <VariationAdd />
          </Route>
          <Route exact path="/edit-variation">  
            <VariationEdit />
          </Route>
  
          <Route exact path="/view-store">  
            <ViewStore />
          </Route>
  
        </Switch>
      </BrowserRouter>
    );
  }else{
    return (
      <BrowserRouter>
        <DomainMainCssLoader />
        <Switch>
          <Route exact path="/">  
            <ViewStore />
          </Route>
        </Switch>
      </BrowserRouter>
    );
  }
  
}

export default App;
