import React, { useState, useEffect } from 'react';
import Sidebar from "../../Layout/Sidebar";
import Navbar from '../../Layout/Navbar';
import Footer from '../../Layout/Footer';
import { Endpoints } from "../../API/Endpoints";
import { CommonApi } from "../../API/CommonApi";
import { FetchApi } from "../../API/FetchApi";
import ToastMessage from "../../Utils/ToastMessage";
import { useHistory } from "react-router-dom";

export default function HeaderAndFooter() {
  const history = useHistory();
  const token = localStorage.getItem("token");

  const [categoryData, setCategoryData] = useState({
    email: "",
    pawerd_by_text: "", // corrected typo (optional)
    contact: "",
    address: "",
    gst: "",
    logo: null,
  });

  const [imagePreview, setImagePreview] = useState(null); // for storing image preview
  const [isLoading, setIsLoading] = useState(false);

  // Handle form input change
  const handleChange = (e) => {
    const { name, value, files } = e.target;

    if (files && files.length > 0) {
      const file = files[0];
      setCategoryData({ ...categoryData, [name]: file });

      // If the file is an image (logo), generate a preview
      if (name === "logo") {
        setImagePreview(URL.createObjectURL(file));
      }
    } else {
      setCategoryData({ ...categoryData, [name]: value });
    }
  };

  // Handle form submission
  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);

    const formData = new FormData();
    formData.append('email', categoryData.email);
    formData.append('pawerd_by_text', categoryData.pawerd_by_text); // corrected key
    formData.append('address', categoryData.address);
    formData.append('contact', categoryData.contact);
    formData.append('gst', categoryData.gst);
    if (categoryData.logo) formData.append('logo', categoryData.logo);

    try {
      const response = await CommonApi(Endpoints.headerfooterupdate, formData);

      if (response && response.status === true) {
        ToastMessage.Success("Header and Footer data updated successfully!");
      } else {
        ToastMessage.Error(response?.message || "Failed to update data.");
      }
    } catch (error) {
      ToastMessage.Error(error?.response?.data?.message || "An error occurred.");
      console.error("Submit Error:", error);
    } finally {
      setIsLoading(false);
    }
  };

  // Fetch existing data
  const getData = async () => {
    try {
      const response = await FetchApi(Endpoints.headerfooter, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });

      if (response?.status === true && response?.data) {
        const { email, pawerd_by_text, contact, address, gst, logo } = response.data;
        setCategoryData((prev) => ({
          ...prev,
          email,
          pawerd_by_text,
          contact,
          address,
          gst,
        }));

        // If the logo exists in the response, set the image preview
        if (logo) {
          setImagePreview(logo); // Assuming `logo` is the full URL
        }
      }
    } catch (error) {
      if (error?.response?.status === 401) {
        ToastMessage.Error("Session expired. Please log in again.");
      } else {
        ToastMessage.Error(error?.response?.data?.message || "Error loading data.");
      }
      console.error("Fetch Error:", error);
    }
  };

  useEffect(() => {
    getData();
  }, []);

  return (
    <div className="page-wrapper">
      <Navbar />
      <Sidebar />
      <div className="page-wrapper">
        <div className="main-content">
          <div className="row">
            <div className="col-12">
              <div className="card">
                <div className="card-header">
                  <h4 className="header-title">Header And Footer</h4>
                </div>
                <div className="card-body">
                  <form onSubmit={handleSubmit} className="parsley-examples">
                    <div className="row">
                      <div className="col-md-6">
                        <div className="mb-3">
                          <label className="form-label">Logo</label>
                          <input
                            type="file"
                            name="logo"
                            onChange={handleChange}
                            className="form-control"
                          />
                        </div>
                      </div>

                      {imagePreview && (
                        <div className="col-md-6">
                          <div className="mb-3">
                            <label className="form-label">Image Preview</label>
                            <div>
                              <img
                                src={imagePreview}
                                alt="Logo Preview"
                                style={{ width: "150px", height: "auto", borderRadius: "5px" }}
                              />
                            </div>
                          </div>
                        </div>
                      )}

                      <div className="col-md-12">
                        <div className="mb-3">
                          <label className="form-label">Email</label>
                          <input
                            type="email"
                            name="email"
                            value={categoryData.email}
                            onChange={handleChange}
                            className="form-control"
                            required
                            placeholder="Enter Email"
                          />
                        </div>
                      </div>

                      <div className="col-md-12">
                        <div className="mb-3">
                          <label className="form-label">Powered By Text</label>
                          <textarea
                            className="form-control"
                            name="pawerd_by_text"
                            required
                            rows="4"
                            placeholder="Enter Powered By Text"
                            onChange={handleChange}
                            value={categoryData.pawerd_by_text}
                          ></textarea>
                        </div>
                      </div>

                      <div className="col-md-6">
                        <div className="mb-3">
                          <label className="form-label">Contact</label>
                          <input
                            type="tel"
                            name="contact"
                            value={categoryData.contact}
                            onChange={handleChange}
                            className="form-control"
                            required
                            placeholder="Enter Contact Number"
                            maxLength="10"  pattern="\d{10}"
                          />
                        </div>
                      </div>

                      <div className="col-md-6">
                        <div className="mb-3">
                          <label className="form-label">Address</label>
                          <input
                            type="text"
                            name="address"
                            value={categoryData.address}
                            onChange={handleChange}
                            className="form-control"
                            required
                            placeholder="Enter Address"
                          />
                        </div>
                      </div>

                      <div className="col-md-6">
                        <div className="mb-3">
                          <label className="form-label">GST</label>
                          <input
                            type="text"
                            name="gst"
                            value={categoryData.gst}
                            onChange={handleChange}
                            className="form-control"
                            required
                            placeholder="Enter GST Number"
                          />
                        </div>
                      </div>

                      <div className="mt-4">
                        <button type="submit" className="btn btn-primary" disabled={isLoading}>
                          {isLoading ? (
                            <>
                              <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                              &nbsp;Loading...
                            </>
                          ) : (
                            "Submit"
                          )}
                        </button>
                      </div>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
        <Footer />
      </div>
    </div>
  );
}
