import React, { useState, useEffect } from 'react';
import Sidebar from "../../Layout/Sidebar";
import Navbar from '../../Layout/Navbar';
import Footer from '../../Layout/Footer';
import { Endpoints } from "../../API/Endpoints";
import { CommonApi } from "../../API/CommonApi";
import { FetchApi } from "../../API/FetchApi";
import ToastMessage from "../../Utils/ToastMessage";
import { useHistory, useLocation } from "react-router-dom";

export default function VariationEdit() {
  const Navigate = useHistory();
  const location = useLocation();
  const item = location?.state?.variationId;

  const [VariationData, setVariationData] = useState({
    id: "",
    type: "",
    value: "",
  });

  const [isLoading, setIsLoading] = useState(false);

  // ✅ Handle form input change
  const handleChange = (e) => {
    const { name, value } = e.target;
    setVariationData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  useEffect(() => {
    if (item) {
      getData();
    }
  }, [item]);

  // ✅ Fetch variation details
  const getData = async () => {
    try {
      const resp = await FetchApi(`${Endpoints.variationDetails}/${item}`);
      if (resp && resp.status === true) {
        setVariationData({
          id: resp.data.id,
          type: resp.data.type || "",
          value: resp.data.value || "",
        });
      } else {
        ToastMessage.Error(resp.message || "Failed to fetch variation details.");
      }
    } catch (e) {
      if (e?.response?.status === 401) {
        ToastMessage.Error("Session Expired! Please login again.");
      } else {
        ToastMessage.Error(e?.response?.data?.message || "Something went wrong.");
      }
    }
  };

  // ✅ Submit updated data
  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);

    const formData = new FormData();
    formData.append("id", VariationData.id);
    formData.append("type", VariationData.type);
    formData.append("value", VariationData.value);

    try {
      const response = await CommonApi(Endpoints.variationUpdate, formData);

      if (response && response.status === true) {
        ToastMessage.Success(response.message || "Variation updated successfully!");
        Navigate.push("/variation");
      } else {
        ToastMessage.Error(response?.message || "Failed to update variation.");
      }
    } catch (error) {
      ToastMessage.Error(error?.response?.data?.message || "An error occurred.");
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div class="page-wrapper">
      <Navbar />
      <Sidebar />
      <div class="page-wrapper">
        <div class="main-content">
          <div className="row">
            <div className="col-12">
                <div className="card">
                  <div className="card-header">
                    <h4 className="header-title">Edit Variation</h4>
                  </div>
                  <div className="card-body">

                    <form onSubmit={handleSubmit} className="parsley-examples">
                      <div className="row">
                      <div className="col-md-6">
                        <div className="mb-3">
                        <label className="form-label">Select Type</label>
                        <select name="type" className="form-control"  value={VariationData.type}
                          onChange={handleChange} required>
                        <option value="" disabled selected>Select Type</option>
                        <option value="Color">Color</option>
                        <option value="Price">Price</option>
                        <option value="Fabric">Fabric</option>
                        <option value="Fit">Fit</option>
                        <option value="Neckline">Neckline</option>
                        <option value="Sleeve Type">Sleeve Type</option>
                        <option value="Waist Type">Waist Type</option>
                        <option value="Pattern">Pattern</option>
                        </select>

                      </div>
                        </div>

                        <div className="col-md-6">
                          <div className="mb-3">
                            <label className="form-label">Value</label>
                            <input
                              type="text"
                              name="value"
                              value={VariationData.value}
                              onChange={handleChange}
                              className="form-control"
                              required
                              placeholder="Enter value"
                            />
                          </div>
                        </div>
                       

                        <div className="mt-4">
                          <button type="submit" className="btn btn-primary" disabled={isLoading}>
                            {isLoading ? (
                              <span>
                                <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                                &nbsp;Loading...
                              </span>
                            ) : (
                              "Submit"
                            )}
                          </button>
                          {/* Optionally you can add a reset button */}
                        </div>
                      </div>
                    </form>
                  </div>
                </div>
             
            </div>
          </div>
        </div>
        <Footer />
      </div>
    </div>
  );
}
