import React, { useState, useEffect } from 'react';
import Sidebar from "../../Layout/Sidebar";
import Navbar from '../../Layout/Navbar';
import Footer from '../../Layout/Footer';
import { Endpoints } from "../../API/Endpoints";
import { CommonApi } from "../../API/CommonApi";
import { FetchApi } from "../../API/FetchApi";
import ToastMessage from "../../Utils/ToastMessage";
import { useHistory, useLocation } from "react-router-dom";
import Select from 'react-select';

export default function ProductEdit() {
  const Navigate = useHistory();
  const location = useLocation();
  const productId = location?.state?.productId;

  const [CollectionList, setCollectionList] = useState([]);
  const [categoryList, setCategoryList] = useState([]);
  const [imagePreview, setImagePreview] = useState([]);
  const [videoPreview, setVideoPreview] = useState('');
  const [categoryData, setCategoryData] = useState({
    id: "",
    image: [],
    video: null,
    sku_code: "",
    name: "",
    description: "",
    collection: "",
    category_id: "",
    size: "",
    qty: "",
  });

  const [fields, setFields] = useState([
    { type: "", value: [], options: [] }
  ]);

  const handleFieldChange = async (index, name, value) => {
    const updatedFields = [...fields];
    if (name === "type") {
      try {
        const resp = await FetchApi(Endpoints.variationData + "/" + value);
        if (resp && resp.status === true) {

          updatedFields[index] = {
            type: value,
            value: [], // reset value when type changes
            options: resp.data || [] // load options based on type
          };

        } else {
          ToastMessage.Error(resp.message || "Failed to fetch categories");
        }
      } catch (e) {
        ToastMessage.Error(e?.response?.data?.message || "Error loading categories");
      }
    } else if (name === "value") {
      updatedFields[index].value = value;
    }

    setFields(updatedFields);

  };

  const addField = () => {
    setFields([...fields, { type: "", value: [], options: [] }]);
  };
  const removeField = (index) => {
    const updatedFields = fields.filter((_, i) => i !== index);
    setFields(updatedFields);
  };

  const [isLoading, setIsLoading] = useState(false);

  const handleChange = (e) => {
    const { name, value, files } = e.target;
    if (name === "image") {
      const files = Array.from(e.target.files);
      const newFiles = files.map(file => ({ file, url: URL.createObjectURL(file) }));

      setImagePreview(prev => [...prev, ...newFiles]);

      setCategoryData({ ...categoryData, image: files });
    } else if (name === "video") {
      const videoFile = files[0];
      setCategoryData(prev => ({ ...prev, video: videoFile }));
      setVideoPreview(URL.createObjectURL(videoFile));

    } else {
      setCategoryData(prev => ({ ...prev, [name]: value }));
    }
    if (name === "collection") {
      getCategoryList(value);  // pass selected collection_id
    }
  };



  const getData = async () => {
    try {
      const resp = await FetchApi(`${Endpoints.ProductDetails}/${productId}`);
      if (resp?.status) {
        const { image, ...rest } = resp.data;
        setCategoryData({ ...rest, image: null });

        const mappedFields = [];

        resp?.data?.variation.forEach(item => {
          const type = item.variation_info.type;
          const value = { value: item.variation_info.id, label: item.variation_info.value };

          // Check if type already exists in mappedFields
          const existingField = mappedFields.find(field => field.type === type);

          if (existingField) {
            // If found, push into value and options
            existingField.value.push(value);
            existingField.options.push(value);
          } else {
            // If not found, create new
            mappedFields.push({
              type: type,
              value: [value],
              options: [value]
            });
          }
        });

        setFields(mappedFields);

        const productImages = resp.data.images.map(img => ({
          id: img.id,
          url: img.image_url,
          file: null // for tracking new uploads
        }));

        setImagePreview(productImages);
        setVideoPreview(resp.data.video);

        getCategoryList(resp.data.collection);

      } else {
        ToastMessage.Error(resp.message || "Failed to fetch product details");
      }
    } catch (e) {
      const msg = e?.response?.data?.message || "An error occurred.";
      if (e?.response?.status === 401) {
        ToastMessage.Error("Session Expired! Please login again.");
      } else {
        ToastMessage.Error(msg);
      }
    }
  };

  const getCategoryList = async (collectionId) => {
    try {
      const resp = await FetchApi(Endpoints.CategoryCollection + "/" + collectionId);
      if (resp?.status) {
        setCategoryList(resp.data);
      } else {
        ToastMessage.Error(resp.message || "Failed to fetch categories");
      }
    } catch (e) {
      ToastMessage.Error(e?.response?.data?.message || "Error loading categories");
    }
  };
  const getCollectionList = async () => {
    try {
      const resp = await FetchApi(Endpoints.Collection);
      if (resp?.status) {
        setCollectionList(resp.data);
      } else {
        ToastMessage.Error(resp.message || "Failed to fetch Collection");
      }
    } catch (e) {
      ToastMessage.Error(e?.response?.data?.message || "Error loading Collection");
    }
  };
  useEffect(() => {
    if (productId) {
      getCollectionList();
      getData();
    } else {
      ToastMessage.Error("No product selected. Please go back and try again.");
      Navigate.push("/product");
    }
  }, [productId]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    const finalData = fields.map(field => ({
      type: field.type,
      value: field.value.map(item => item.value)
    }));

    const mergedValues = finalData.reduce((acc, field) => acc.concat(field.value), []);

    setIsLoading(true);

    const productForm = new FormData();
    productForm.append("id", categoryData.id);
    productForm.append("sku_code", categoryData.sku_code);
    productForm.append("name", categoryData.name);
    productForm.append("description", categoryData.description);
    productForm.append("collection", categoryData.collection);
    productForm.append("category_id", categoryData.category_id);
    productForm.append("size", categoryData.size);
    productForm.append("qty", categoryData.qty);
    mergedValues.forEach(value => {
      productForm.append("variation_id[]", value);
    });
    if (Array.isArray(categoryData.image)) {
      categoryData.image?.forEach((imageFile, index) => {
        productForm.append("image[]", imageFile);
      });
    }
    if (categoryData.video) {
      productForm.append("video", categoryData.video);
    }
    
    try {
      const response = await CommonApi(Endpoints.ProductUpdate, productForm);
      if (response?.status) {
        ToastMessage.Success(response.message);
        Navigate.push("/product");
      } else {
        ToastMessage.Error(response?.message || "Failed to update product.");
      }
    } catch (error) {
      ToastMessage.Error(error?.response?.data?.message || "An error occurred while updating the product.");
    } finally {
      setIsLoading(false);
    }
  };

  const handleRemoveImage = (index) => {
    setImagePreview(prev => prev.filter((_, i) => i !== index));
  };
  const handleDeleteImage = async (Id, index) => {
    try {
      let resp = await FetchApi(Endpoints.ProductImageDelete + "/" + Id);
      if (resp && resp.status == true) {
        setImagePreview(prev => prev.filter((_, i) => i !== index));
      } else {
        ToastMessage.Error(resp.message);
      }
    } catch (e) {
      if (e && e.response && e.response.data && e.response.data.message) {
        if (e.response.status === 401) {
          ToastMessage.Error("Session Expired! Please login again.");
        } else {
          ToastMessage.Error(e.response.data.message);
        }
      }
    }
  };



  return (
    <div class="page-wrapper">
      <Navbar />
      <Sidebar />
      <div class="page-wrapper">
        <div class="main-content">

          <div className="card">
            <div className="card-header">
              <h4 className="header-title">Edit Product</h4>
            </div>
            <div className="card-body">

              <form onSubmit={handleSubmit} encType="multipart/form-data">
                <div className="row">
                  {/* Left Column */}

                  <div className="col-md-6">
                    <div className="mb-3">
                      <label className="form-label">Product Name</label>
                      <input type="text" name="name" className="form-control" value={categoryData.name} onChange={handleChange} required />
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="mb-3">
                      <label className="form-label">SKU Code</label>
                      <input type="text" name="sku_code" className="form-control" value={categoryData.sku_code} onChange={handleChange} required />
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="mb-3">
                      <label className="form-label">Quantity</label>
                      <input type="number" name="qty" className="form-control" value={categoryData.qty} onChange={handleChange} required />
                    </div>

                  </div>
                  <div className="col-md-6">
                    <div className="mb-3">
                      <label className="form-label">Product Photo</label>
                      <input type="file" name="image" className="form-control" multiple onChange={handleChange}
                        accept="image/png, image/jpeg, image/jpg, image/gif, image/webp"
                      />
                    </div>
                  </div>

                  <div className="col-md-12">
                    <div className="row">
                      {imagePreview.map((img, index) => (
                        <div className="col-md-2 mb-3" key={index}>
                          <div className="card">
                            <img
                              src={img.file ? URL.createObjectURL(img.file) : img.url}
                              alt="preview"
                              className="card-img-top"
                              style={{ height: "100px", objectFit: "cover" }}
                            />
                            <div className="card-body p-2">
                              {img.file ?
                                <button
                                  type="button"
                                  className="btn btn-sm btn-danger w-100"
                                  onClick={() => handleRemoveImage(index)}
                                >
                                  Remove
                                </button>
                                :
                                <button
                                  type="button"
                                  className="btn btn-sm btn-danger w-100"
                                  onClick={() => handleDeleteImage(img.id, index)}
                                >
                                  Remove
                                </button>
                              }

                            </div>
                          </div>
                        </div>
                      ))}
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="mb-3">
                      <label className="form-label">Product Video</label>
                      <input type="file" name="video" className="form-control" onChange={handleChange} accept="video/*" />
                    </div>
                  </div>
                  {categoryData.video && (
                    <div className="col-md-6">
                      <div className="mb-3">

                        <video
                          controls
                          width="300"
                          src={videoPreview}
                          style={{ borderRadius: "8px" }}
                        />
                      </div>
                    </div>
                  )}
                  <div className="col-md-6">
                    <div className="mb-3">
                      <label className="form-label">Select Collection</label>
                      <select name="collection" className="form-control" value={categoryData.collection} onChange={handleChange} required>
                        <option value="">Choose Collection</option>
                        {CollectionList.map((colle) => (
                          <option key={colle.id} value={colle.id}>{colle.name}</option>
                        ))}
                      </select>
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="mb-3">
                      <label className="form-label">Select Category</label>
                      <select name="category_id" className="form-control" value={categoryData.category_id} onChange={handleChange} required>
                        <option value="" >Choose Category</option>
                        {categoryList.map((cat) => (
                          <option key={cat.id} value={cat.id}>{cat.name}</option>
                        ))}
                      </select>
                    </div>
                  </div>


                  {/* Right Column */}
                  <div className="col-md-4">
                    <div className="mb-3">
                      <label className="form-label">Select Size</label>
                      <select name="size" className="form-control" value={categoryData.size} onChange={handleChange} required>
                        <option value="">Choose Size</option>
                        <option value="S">Small</option>
                        <option value="M">Medium</option>
                        <option value="L">Large</option>
                      </select>
                    </div>
                  </div>


                  <div className="col-md-12">
                    <div className="mb-3">
                      <label className="form-label">Description</label>
                      <textarea name="description" className="form-control" value={categoryData.description} onChange={handleChange} required />
                    </div>

                  </div>

                  <div className="col-md-12">
                    {fields.map((field, index) => (
                      <div className="row mb-3" key={index}>
                        <div className="col-md-4">
                          <label className="form-label">Select Variation</label>
                          <select
                            name="type"
                            className="form-control"
                            value={field.type}
                            onChange={(e) => handleFieldChange(index, "type", e.target.value)}
                            required
                          >
                            <option value="" disabled>Select Type</option>
                            <option value="Color">Color</option>
                            <option value="Price">Price</option>
                            <option value="Fabric">Fabric</option>
                            <option value="Fit">Fit</option>
                            <option value="Neckline">Neckline</option>
                            <option value="SleeveType">Sleeve Type</option>
                            <option value="WaistType">Waist Type</option>
                            <option value="Pattern">Pattern</option>
                          </select>
                        </div>

                        <div className="col-md-5">
                          <label className="form-label">Value</label>
                          <Select
                            isMulti
                            name="value"
                            options={field.options.map(opt => ({ value: opt.id, label: opt.value }))}
                            value={field.value.map(val => ({ value: val.value, label: val.label }))}
                            onChange={(selectedOptions) =>
                              handleFieldChange(index, "value", selectedOptions.map(option => option))
                            }
                            className="basic-multi-select"
                            classNamePrefix="select"
                          />


                        </div>

                        <div className="col-md-3 d-flex align-items-end gap-2">
                          {index !== 0 && (
                            <button
                              type="button"
                              className="btn btn-secondary"
                              onClick={() => removeField(index)}
                            >
                              Remove
                            </button>
                          )}
                          {index === 0 && (
                            <button
                              type="button"
                              className="btn btn-primary"
                              onClick={addField}
                            >
                              + Add More
                            </button>
                          )}
                        </div>
                      </div>
                    ))}

                  </div>

                  <div className="mt-4">
                    <button type="submit" className="btn btn-primary" disabled={isLoading}>
                      {isLoading ? "Submitting..." : "Submit"}
                    </button>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>

        <Footer />
      </div>
    </div>
  );
}
