import { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";

const DomainMainCssLoader = ({ children }) => {
  const location = useLocation();
  const [loaded, setLoaded] = useState(false);

  useEffect(() => {
    const head = document.head;

    // Remove old dynamic CSS
    const oldLinks = document.querySelectorAll("link[data-dynamic]");
    oldLinks.forEach(link => link.remove());

    const loadCss = (href) => {
      return new Promise((resolve, reject) => {
        const link = document.createElement("link");
        link.rel = "stylesheet";
        link.href = href;
        link.setAttribute("data-dynamic", "true");
        link.onload = () => resolve();
        link.onerror = () => reject(`Failed to load: ${href}`);
        head.appendChild(link);
      });
    };

    const cssList = [
            "assets/css/fonts.css",
            "assets/css/bootstrap.min.css",
            "assets/css/auth.css",
            "assets/css/font-awesome.min.css",
            "assets/css/icofont.min.css",
            "assets/css/style.css",
            "assets/css/datatables.css",
            "assets/css/newStyle.css",
          ];

    setLoaded(false); // show loader

    Promise.all(cssList.map(loadCss))
      .then(() => setLoaded(true))
      .catch((err) => {
        console.error("CSS Load Error:", err);
        setLoaded(true); // fail gracefully
      });
  }, [location.pathname]);

  if (!loaded) {
    return (
        <div
            className="d-flex justify-content-center align-items-center vh-100"
            style={{ backgroundColor: "#f8f9fa" }}
        >
            <div className="spinner-border text-primary" role="status">
            <span className="visually-hidden">Loading...</span>
            </div>
        </div>
    );
  }

  return <>{children}</>;
};

export default DomainMainCssLoader;
