import React, { useState } from "react";
import { Endpoints } from "../../API/Endpoints";
import { CommonApi } from "../../API/CommonApi";
import ToastMessage from "../../Utils/ToastMessage";
import { useHistory } from "react-router-dom";
import { Link } from "react-router-dom/cjs/react-router-dom.min";

export default function Register() {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [message, setMessage] = useState("");

  const [nameError, setNameError] = useState("");
  const [emailError, setEmailError] = useState("");
  const [passwordError, setPasswordError] = useState("");

  const history = useHistory();


  const handleSubmit = async (e) => {
    e.preventDefault();

    // Validate inputs
    let valid = true;



    if (!name.trim()) {
      setNameError("Please enter your name");
      valid = false;
    }

    if (!email.trim()) {
      setEmailError("Please enter your email");
      valid = false;
    }

    if (!password) {
      setPasswordError("Please enter a password");
      valid = false;
    }

    if (!valid) return;

    const formDataToSend = new FormData();
    formDataToSend.append("name", name);
    formDataToSend.append("email", email);
    formDataToSend.append("password", password);

    try {
      const response = await CommonApi(Endpoints.Register, formDataToSend);

      if (response && response.status === true) {
        ToastMessage.Success(response.message);
        localStorage.setItem("token", response.token);
        localStorage.setItem("user", JSON.stringify(response.user));
        history.push("/profile");
      }
    } catch (error) {
      setMessage(error?.response?.data?.error || "Registration failed.");
    }
  };

  return (
    <div className="splash-auth-wrapper">
      <div className="splash-auth-box">
        <div className="splash-auth-content">

          <Link to="/dashboard">
            <img src="assets/logo.png" alt="Logo" className="w-50" />
          </Link>
          <h2>Hello, Welcome to <span>Quicklog Tab</span></h2>
          <p>Please fill in the details below to create your account.</p>

          {message && <div className="alert alert-danger">{message}</div>}

          {/* Form */}
          <form onSubmit={handleSubmit}>


            {/* Name */}
            <div className="mb-3">

              <input
                className="form-control"
                type="text"
                id="name"
                value={name}
                onChange={(e) => {
                  setName(e.target.value);
                  setNameError("");
                }}
                placeholder="Enter your name"
              />
              {nameError && <small className="text-danger">{nameError}</small>}
            </div>

            {/* Email */}
            <div className="mb-3">

              <input
                className="form-control"
                type="email"
                id="email"
                value={email}
                onChange={(e) => {
                  setEmail(e.target.value);
                  setEmailError("");
                }}
                placeholder="Enter your email"
              />
              {emailError && <small className="text-danger">{emailError}</small>}
            </div>

            {/* Password */}
            <div className="mb-3">

              <input
                type="password"
                id="password"
                className="form-control"
                value={password}
                onChange={(e) => {
                  setPassword(e.target.value);
                  setPasswordError("");
                }}
                placeholder="Enter your password"
              />
              {passwordError && <small className="text-danger">{passwordError}</small>}
            </div>

            {/* Submit */}
            <div className="text-center d-grid">
              <button className="btn btn-primary waves-effect waves-light" type="submit">
                Sign Up
              </button>
            </div>

            {/* Social login */}
            {/* <div className="text-center mt-4">
              <p className="text-muted font-16">Sign in with</p>
              <ul className="social-list list-inline mt-3 mb-0">
                <li className="list-inline-item">
                  <a href="#" className="social-list-item border-primary text-primary">
                    <img src="assets/images/google-login.jpg" className="w-75" alt="Google Login" />
                  </a>
                </li>
              </ul>
            </div> */}
          </form>
          <p className="text-muted mt-2 pt-3">
                Already have an account?
                <Link to="/" className="text-muted ms-1">
                  <b>Log In</b>
                </Link>
              </p>
        </div>
      </div>
    </div>
  );
}
