import React, { useState, useEffect } from 'react';
import Sidebar from "../../Layout/Sidebar";
import Navbar from '../../Layout/Navbar';
import Footer from '../../Layout/Footer';
import { Endpoints } from "../../API/Endpoints";
import { FetchApi } from "../../API/FetchApi";
import { CommonApi } from "../../API/CommonApi";
import ToastMessage from "../../Utils/ToastMessage";
import { useHistory } from "react-router-dom";

export default function CategoryAdd() {
  const Navigate = useHistory();

  // State to store form data
  const [categoryData, setCategoryData] = useState({
    name: "",
    description: "",
    collection_id: "",
    image: null
  });

  // State to handle the loading state of the submit button
  const [isLoading, setIsLoading] = useState(false);
  const [allCollectionList, setAllCollectionList] = useState([]);

  // Handle changes in form inputs
  const handleChange = (e) => {
    const { name, value, files } = e.target;
    if (name === "image") {
      setCategoryData({ ...categoryData, image: files[0] });
    } else {
      setCategoryData({ ...categoryData, [name]: value });
    }
  };

  // Handle form submission
  const handleSubmit = async (e) => {
    e.preventDefault();

    // Set loading to true to show the loading state on the button
    setIsLoading(true);

    // Creating FormData object to send image and text data
    const formData = new FormData();
    formData.append('name', categoryData.name);
    formData.append('description', categoryData.description);
    formData.append('image', categoryData.image);
    formData.append('collection_id', categoryData.collection_id);


    try {
      const response = await CommonApi(Endpoints.categorystore, formData);

      if (response && response.status === true) {
        ToastMessage.Success("Category created successfully!");
        console.log("Response:", response.data);

        // Reset form after successful submission
        setCategoryData({
          name: "",
          description: "",
          collection_id: "",
          image: null
        });

        // Redirect after successful category creation
        Navigate.push("/category");
      } else {
        ToastMessage.Error(response?.message || "Failed to create category.");
      }
    } catch (error) {
      ToastMessage.Error(error?.response?.message || "An error occurred while creating the category.");
      console.error("Error:", error);
    } finally {
      // Set loading to false after the API call is complete (success or error)
      setIsLoading(false);
    }
  };

const getSubcollectionList = async () => {
    try {
      let resp = await FetchApi(Endpoints.Collection);
      if (resp && resp.status === true) {
        setAllCollectionList(resp.data);
      } else {
        ToastMessage.Error(resp.message);
      }
    } catch (e) {
      if (e?.response?.status === 401) {
        ToastMessage.Error("Session Expired! Please login again.");
      } else {
        ToastMessage.Error(e?.response?.data?.message || "Error fetching collections.");
      }
    }
  };

  useEffect(() => {
    getSubcollectionList();
  }, []);
  
  return (
    <div class="page-wrapper">
      <Navbar />
      <Sidebar />
      <div class="page-wrapper">
        <div class="main-content">
          <div className="row">
            <div className="col-12">
             
                <div className="card">
                  <div className="card-header">
                    <h4 className="header-title">Create Category</h4>
                  </div>
                  <div className="card-body">

                    <form onSubmit={handleSubmit} className="parsley-examples">
                      <div className="row">
                        <div className="col-md-6">
                          <div className="mb-3">
                            <label className="form-label">Collection</label>
                           <select className="form-control"   onChange={handleChange} name="collection_id" >
                               <option> Select ollection</option>
                                {allCollectionList.map((item) => (
                                  <option key={item.id} value={item.id}>
                                    {item.name}
                                  </option>
                              ))}
                           </select>
                          </div>
                        </div>
                        <div className="col-md-6">
                          <div className="mb-3">
                            <label className="form-label">Name</label>
                            <input
                              type="text"
                              name="name"
                              value={categoryData.name}
                              onChange={handleChange}
                              className="form-control"
                              required
                              placeholder="Enter Name"
                            />
                          </div>
                        </div>

                        <div className="col-md-6">
                          <div className="mb-3">
                            <label className="form-label">Category Image</label>
                            <input
                              type="file"
                              name="image"
                              onChange={handleChange}
                              className="form-control"
                              required
                            />
                          </div>
                        </div>
                        <div className="col-md-12">
                          <div className="mb-3">
                            <label className="form-label">Description</label>
                            <textarea className="form-control"
                              name="description"
                              required
                              rows="4"
                              cols="50"
                              placeholder="Enter Description"
                              onChange={handleChange}
                              value={categoryData.description}></textarea>

                          </div>
                        </div>

                        <div className="mt-4">
                          <button type="submit" className="btn btn-primary" disabled={isLoading}>
                            {isLoading ? (
                              <span>
                                <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                                &nbsp;Loading...
                              </span>
                            ) : (
                              "Submit"
                            )}
                          </button>
                          {/* Optionally you can add a reset button */}
                        </div>
                      </div>
                    </form>
                  </div>
                </div>
             
            </div>
          </div>
        </div>
        <Footer />
      </div>
    </div>
  );
}
