import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import { Table, THead, TH, TR, TD, TBody } from "../table/index";
import { Link } from "react-router-dom";
import Sidebar from "../../Layout/Sidebar";
import Navbar from '../../Layout/Navbar';
import Footer from '../../Layout/Footer';
import { FetchApi } from "../../API/FetchApi";
import { DeleteApi } from "../../API/DeleteApi";
import { Endpoints } from "../../API/Endpoints";
import ToastMessage from "../../Utils/ToastMessage";
import Swal from "sweetalert2";
const $ = window.jQuery;
    export class Collection extends Component {

      constructor(props) {
        super(props);
        this.state = {
          allcollectionList: [],
        };
      }
    
      componentDidMount() {
        this.getSubcollectionList();
      }
    
      getSubcollectionList = async () => {
        try {
          let resp = await FetchApi(Endpoints.Collection);
          if (resp && resp.status == true) {
            this.setState({
              allcollectionList: resp.data,
            });
            $("#example").dataTable();
    
          } else {
            ToastMessage.Error(resp.message);
          }
        } catch (e) {
          if (e && e.response && e.response.data && e.response.data.message) {
            if (e.response.status === 401) {
              ToastMessage.Error("Session Expired! Please login again.");
            } else {
              ToastMessage.Error(e.response.data.message);
            }
          }
        }
    
      };
    
      onEditcollection = (collectionId) => {
        this.props.history.push("edit-collection", { collectionId: collectionId });
      };
    
      onDeletecollection = (id, name) => {
        Swal.fire({
          title: "Are you sure?",
          text: `You won't be able to revert ${name}!`,
          icon: "warning",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "Yes, delete it!"
        }).then((result) => {
          if (result.isConfirmed) {
            this.DeleteData(id);
          } else {
            ToastMessage.Info("Your data is safe!");
          }
        });
      };
    
      DeleteData = async (id) => {
        try {
          let resp = await FetchApi(Endpoints.CollectionDelete + "/" + id);
          if (resp && resp.status == true) {
            ToastMessage.Success(resp.message);
            var array = [...this.state.allcollectionList];
            let newData = array.filter((item) => item.id !== id);
            this.setState({ allcollectionList: newData });
            if (newData.length === 0) {
              $("#example").DataTable().clear().draw();
            }
    
          } else {
            ToastMessage.Error(resp.message);
          }
        } catch (e) {
          if (e && e.response && e.response.data && e.response.data.message) {
            if (e.response.status === 401) {
              ToastMessage.Error("Session Expired! Please login again.");
            } else {
              ToastMessage.Error(e.response.data.message);
            }
          }
        }
      };
      render() {
        return (
          <div class="page-wrapper">
            <Navbar />
            <Sidebar />
            <div class="page-wrapper">
              <div class="main-content">
    
                <div className="row">
                  <div className="col-12">
                    <div className="card">
                      <div className="card-header border-bottom py-1">
    
                        <div className="d-flex justify-content-between">
    
                          <h4 className="page-title">collection</h4>
    
    
                          <Link to="/add-collection" className="btn btn-primary">
                            Add New
                          </Link>
    
                        </div>
                      </div>
                      <div className="card-body">
                        <div style={{ overflowX: 'auto' }}>
                          <Table id="example" className="table w-100">
                            <THead>
                              <TR>
                                <TH>S.NO.</TH>
                                <TH>Name</TH>
                                <TH>Image</TH>
                                <TH>Action</TH>
                              </TR>
                            </THead>
                            <TBody>
                              {this.state?.allcollectionList.map((item, index) => {
                                return (
                                  <TR key={item.subcollectionid}>
                                    <TD>{index + 1}</TD>
                                    <TD>{item.name}</TD>
                                    <TD>
                                      <img src={item.image} alt={item.name} className="img-thumbnail" style={{ width: '50px' }} />
                                    </TD>
    
    
                                    <TD class="relative">
    
                                      <a class="action-btn " href="javascript:void(0); ">
                                        <svg class="default-size " viewBox="0 0 341.333 341.333 ">
                                          <g>
                                            <g>
                                              <g>
                                                <path d="M170.667,85.333c23.573,0,42.667-19.093,42.667-42.667C213.333,19.093,194.24,0,170.667,0S128,19.093,128,42.667 C128,66.24,147.093,85.333,170.667,85.333z "></path>
                                                <path d="M170.667,128C147.093,128,128,147.093,128,170.667s19.093,42.667,42.667,42.667s42.667-19.093,42.667-42.667 S194.24,128,170.667,128z "></path>
                                                <path d="M170.667,256C147.093,256,128,275.093,128,298.667c0,23.573,19.093,42.667,42.667,42.667s42.667-19.093,42.667-42.667 C213.333,275.093,194.24,256,170.667,256z "></path>
                                              </g>
                                            </g>
                                          </g>
                                        </svg>
                                      </a>
                                      <div class="action-option ">
                                        <ul>
                                          <li>
                                            <a href="javascript:void(0); " onClick={() =>
                                              this.onEditcollection(item.id)
                                            }><i class="far fa-edit mr-2 "></i>Edit</a>
                                          </li>
                                          <li>
                                            <a href="javascript:void(0); " onClick={() =>
                                              this.onDeletecollection(item.id, item.name)
                                            }><i class="far fa-trash-alt mr-2 "></i>Delete</a>
                                          </li>
                                        </ul>
                                      </div>
                                    </TD>
                                  </TR>
                                );
                              })}
    
                            </TBody>
                          </Table>
                        </div>
    
                      </div>
                    </div>
                  </div>
                </div>
              </div>
    
              <Footer />
            </div>
          </div>
        );
      }
    
    }
    
    export default withRouter(Collection);
